import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Divider,
    Radio,
    Space,
    Typography,
} from 'antd';
import './index.less';
import moment from 'moment';
import { formatDate_hh_mm_ss } from '@/utils/formatDate';

const { Paragraph, Link } = Typography;

class AntiMoneyFormB extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            btnType: '1', //1保存，2next
            postMessagedata: this.props.postMessagedata,
            formdata: this.props.formdata,
        };
    }


    componentDidMount() {
        console.log(this.props);
        // this.props.formB(this);
        this.CheckBtn();
    }

    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        if (
            JSON.stringify(nextProps.postMessagedata) !== this.props.postMessagedata
        ) {
            this.setState({ postMessagedata: nextProps.postMessagedata }, () => {
                this.formRef.current.resetFields();
                this.CheckBtn();
            });
        }
        if (JSON.stringify(nextProps.formdata) !== this.props.formdata) {
            this.setState({ formdata: nextProps.formdata }, () => {
                this.formRef.current.resetFields();
                this.CheckBtn();
            });
        }
    }

    goback = () => {
        let { formdata } = this.state;
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formB');
                if (formdata.formBod) {
                    this.props.changeShowtype('formBod');
                } else {
                    this.props.changeShowtype('formA');
                }
            })
            .catch((errorInfo) => {
                this.props.saveFormData(errorInfo.values, 'formB');
                if (formdata.formBod) {
                    this.props.changeShowtype('formBod');
                } else {
                    this.props.changeShowtype('formA');
                }
            });
    };

    formRef = React.createRef();

    jumpNext = (type) => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formB');
                if (type === 1) {
                    this.props.saveFormData(false, 'formE');
                    this.props.changeShowtype('formCate');
                } else {
                    this.props.changeShowtype('formE');
                }
            })
            .catch((errorInfo) => { });
    };

    CheckBtn = () => {
        let value = this.formRef.current.getFieldsValue([
            'highRiskCustomer',
            'fatf',
            'pep',
        ]);
        console.log(Object.values(value));
        if (Object.values(value).includes(true)) {
            this.setState({ btnType: '2' });
        } else {
            this.setState({ btnType: '1' });
        }
    };

    tosave = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formB', true);
                // this.props.tosaveInfo()
            })
            .catch((errorInfo) => { });
    };

    toClose = () => {
        let postMessageData = {
            key: 'close',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

    render() {
        const { btnType, postMessagedata, formdata } = this.state;
        const layout = {
            labelCol: {
                span: 10,
            },
            wrapperCol: {
                span: 14,
            },
        };
        const layout2 = {
            labelCol: {
                span: 5,
            },
            wrapperCol: {
                span: 19,
            },
        };
        const ruleConfig = {
            rules: [{ required: true, message: ' ' }],
        };
        const disabledConfig = {
            disabled: formdata.disabled ? formdata.disabled : false,
        };
        let initialValuesConfig =
            formdata && formdata.formB
                ? { ...formdata.formB, }
                : {
                    outletCode: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch.cStoreCode
                        : '',
                    cddDate: postMessagedata.data
                        ? moment(postMessagedata.data.rm_retailvouch.createTime).format(formatDate_hh_mm_ss)
                        : '',
                    employeeName: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch
                            .retailVouchHeaderExtend_Employeename
                        : '',
                    terrorist: postMessagedata.checkData
                        ? postMessagedata.checkData.terrorist
                        : '',
              sanction: postMessagedata?.checkData
                        ? postMessagedata.checkData.sanction
                        : '',
                    pep: postMessagedata.checkData ? postMessagedata.checkData.pep : '',
                    fatf: postMessagedata.checkData
                        ? postMessagedata.checkData.fatf
                        : '',
                    highRiskCustomer: postMessagedata.checkData
                        ? postMessagedata.checkData.highRiskCustomer
                        : '',
              orderId: postMessagedata?.formData?.id || '',
                };
        return (
          <div className="antiMoneyFormB">
            <Form
              {...layout}
              name="formB"
              ref={this.formRef}
              colon={false}
              requiredMark={false}
              initialValues={{
                ...initialValuesConfig,
              }}
              // onFinish={onFinish}
              // onFinishFailed={onFinishFailed}
            >
              <Row>
                <Col span={24}>
                  <div className="title">
                    Customer Due Diligence(CDD)-FORM B
                  </div>
                </Col>
                <Col span={12}>
                  <Form.Item
                    // {...ruleConfig}
                    label="Record No."
                    name="orderId"
                    // rules={[
                    //   {
                    //     required: true,
                    //     message: 'Please input your username!',
                    //   },
                    // ]}
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Outlet" name="outletCode">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Date" name="cddDate">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Name of Employee"
                    name="employeeName"
                    {...layout2}
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Divider />
                <Col span={24}>
                  <div className="title">
                    B1. Checking Against Terrorist Designations/Sanction Lists
                  </div>
                </Col>
                <Col span={16}>
                  <Typography>
                    <Paragraph>
                      1. Ministry of Home Affairs (MHA)’s website on the
                      Inter-Ministry Committee on Terrorist Designation
                      (“IMC-TD”)for information on terrorist designation
                      (persons and entities designated as terrorists)
                    </Paragraph>
                    <Paragraph>
                      * Important If yes, to stop transaction anp lodge a police
                      report
                    </Paragraph>
                  </Typography>
                </Col>
                <Col span={8} className="topLable">
                  <Form.Item
                    {...ruleConfig}
                    label="Any match?"
                    name="terrorist"
                  >
                    {/* 等接口 */}
                    <Radio.Group {...disabledConfig}>
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Link
                  href=" https://www.mha.gov.sg/inter-ministry-committee-terrorist-designation-(imc-td)"
                  target="_blank"
                  className="linkhref addBottom"
                  underline
                >
                  https://www.mha.gov.sg/inter-ministry-committee-terrorist-designation-(imc-td)
                </Link>
                <Col span={16}>
                  <Typography>
                    <Paragraph>
                      2. Monetary Authority of Singapore (MAS”)’s website on
                      targeted financial sanctions under the United Nations
                      Regulations (UN Regulations”)for the lists of designated
                      individuals and entities
                    </Paragraph>
                    <Paragraph>
                      * Important If yes, to stop transaction anp lodge a police
                      report
                    </Paragraph>
                  </Typography>
                </Col>
                <Col span={8} className="topLable">
                  <Form.Item {...ruleConfig} label="Any match?" name="sanction">
                    <Radio.Group {...disabledConfig}>
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Link
                  href=" https://www.mas.gov.sg/requlation/anti-money-laundering/targeted-financial-sanctions/lists-of-designated-individuas-and-entities"
                  target="_blank"
                  className="linkhref"
                  underline
                >
                  https://www.mas.gov.sg/requlation/anti-money-laundering/targeted-financial-sanctions/lists-of-designated-individuas-and-entities
                </Link>
                <Divider />
                <Col span={24}>
                  <div className="title">
                    B2. Politically Exposed Person(PEP)
                  </div>
                </Col>
                <Col span={16}>
                  <Typography>
                    <Paragraph>
                      1. Is the customer a Politically Exposed Person(PEP)?
                    </Paragraph>
                    <Paragraph>
                      * Important: If yes: o proceed with ECDD
                    </Paragraph>
                  </Typography>
                </Col>
                <Col span={8} className="topLable">
                  <Form.Item {...ruleConfig} label=" " name="pep">
                    <Radio.Group {...disabledConfig} onChange={this.CheckBtn}>
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Divider />
                <Col span={24}>
                  <div className="title">
                    B3. Check against FATF Public List of High-Risk and Other
                    Monitored Jurisdictions
                  </div>
                </Col>
                <Col span={16}>
                  <Typography>
                    <Paragraph>
                      1. Is the customer from a FATF public list on high-risk
                      and non-cooperative country?
                    </Paragraph>
                    <Paragraph>
                      * Important: If yes: lo proceed with ECDD
                    </Paragraph>
                  </Typography>
                </Col>
                <Col span={8} className="topLable">
                  <Form.Item {...ruleConfig} label=" " name="fatf">
                    <Radio.Group {...disabledConfig} onChange={this.CheckBtn}>
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Link
                  href="www.fatf-gatf.org/countries/#high-risk"
                  target="_blank"
                  className="linkhref"
                  underline
                >
                  www.fatf-gatf.org/countries/#high-risk
                </Link>
                <Divider />
                <Col span={24}>
                  <div className="title">
                    B4. Higher-Risk Customer Based on Risk Assessment Conducted
                    by Regulated Dealer
                  </div>
                </Col>
                <Col span={16}>
                  <Typography>
                    <Paragraph>
                      1. Does the customer fit the profile of a higher-risk
                      customer by displaying any suspicious or red flag
                    </Paragraph>
                    <Paragraph>
                      * Important: If yes: lo proceed with CDD
                    </Paragraph>
                  </Typography>
                </Col>
                <Col span={8} className="topLable">
                  <Form.Item {...ruleConfig} label=" " name="highRiskCustomer">
                    <Radio.Group {...disabledConfig} onChange={this.CheckBtn}>
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Divider />
              </Row>
            </Form>
            <div className="btn">
              {this.props.printnow ? (
                ''
              ) : (
                <Space>
                  {formdata.disabled === undefined && (
                    <Button onClick={this.toClose}>Close</Button>
                  )}
                  <Button onClick={this.goback}>Back</Button>
                  {btnType === '1' ? (
                    !formdata.disabled ? (
                      // 去签名 // <Button onClick={this.tosave}>Save</Button>
                      <Button onClick={() => this.jumpNext(1)}>Next</Button>
                    ) : (
                      <Button onClick={() => this.jumpNext(1)}>Next</Button>
                    )
                  ) : (
                    <Button onClick={() => this.jumpNext(2)}>Next</Button>
                  )}
                </Space>
              )}
            </div>
          </div>
        );
    }
}

export default AntiMoneyFormB;
