/*
进行local数据存储管理的工具模块
 */
import store from 'store'
import { aesEncrypt, aesDecode } from './aesUtils'

const JO_TOKEN = 'JO_TOKEN'
const JO_USER_INFO = 'JO_USER_INFO' // 当前登录人信息
const JO_USER_STORES = 'JO_USER_STORES' // jo 门店信息
const JO_STORE_ID = 'JO_STORE_ID' // 当前登录人所属的门店ID
const JO_STROE_BRAND = 'JO_STROE_BRAND' // 当前门店类别
const JO_USER_ORGS = 'JO_USER_ORGS' // 当前用户组织
const JO_EMPLOYEE_INFO = 'JO_EMPLOYEE_INFO' // 当前员工信息
const JO_STATUS = 'JO_STATUS' // jo 的状态


const storageUtils = {
    /*  保存-读取-删除 Token */
    saveToken: (v) => store.set(JO_TOKEN, aesEncrypt(v)),
    getToken: () => aesDecode(store.get(JO_TOKEN)) || null,
    removeToken: () => store.remove(JO_TOKEN),

    /*  保存-读取-删除 user */
    saveUser: (v) => store.set(JO_USER_INFO, aesEncrypt(v)),
    getUser: () => aesDecode(store.get(JO_USER_INFO)) || null,
    removeUser: () => store.remove(JO_USER_INFO),

    /*  保存-读取-删除 store */
    saveStore: (v) => store.set(JO_USER_STORES, aesEncrypt(v)),
    getStore: () => aesDecode(store.get(JO_USER_STORES)) || null,
    removeStore: () => store.remove(JO_USER_STORES),

    /*  保存-读取-删除 JO_STORE_ID */
    saveCurrentStoreId: (v) => store.set(JO_STORE_ID, aesEncrypt(v)),
    getCurrentStoreId: () => aesDecode(store.get(JO_STORE_ID)) || null,
    removeCurrentStoreId: () => store.remove(JO_STORE_ID),

    /*  保存-读取-删除 JO_STROE_BRAND */
    saveCurrentStoreBrand: (v) => store.set(JO_STROE_BRAND, aesEncrypt(v)),
    getCurrentStoreBrand: () => aesDecode(store.get(JO_STROE_BRAND)) || null,
    removeCurrentStoreBrand: () => store.remove(JO_STROE_BRAND),

    /*  保存-读取-删除 JO_USER_ORGS */
    saveUserOrgs: (v) => store.set(JO_USER_ORGS, aesEncrypt(v)),
    getUserOrgs: () => aesDecode(store.get(JO_USER_ORGS)) || null,
    removeUserOrgs: () => store.remove(JO_USER_ORGS),

    /*  保存-读取-删除 JO_EMPLOYEE_INFO */
    saveEmployeeInfo: (v) => store.set(JO_EMPLOYEE_INFO, aesEncrypt(v)),
    getEmployeeInfo: () => aesDecode(store.get(JO_EMPLOYEE_INFO)) || null,
    removeEmployeeInfo: () => store.remove(JO_EMPLOYEE_INFO),

    /*  保存-读取-删除 JO_STATUS */
    saveJoStatus: (v) => store.set(JO_STATUS, aesEncrypt(v)),
    getJoStatus: () => aesDecode(store.get(JO_STATUS)) || [],
    removeJoStatus: () => store.remove(JO_STATUS),


}


export default storageUtils
