import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Space,
    Spin,
    Table,
    DatePicker,
    Pagination,
    Modal,
    Tooltip,
    Select
} from 'antd';
import './index.less';
import { amlRecord, getamlform, /* toGetStoreList, */ getAtta } from '@/api/antiM';
import { LinkOutlined } from '@ant-design/icons';
import SelectMultipleCommon from '@/components/selectCommon/selectMultiple';
import moment from 'moment';
import AntiMoney from '../antiMoney';
import Notification from '@/components/Notification/index'
import { getUrlParams } from '@/utils/getUrl'

const { Option } = Select;
const { RangePicker } = DatePicker;
const format = 'YYYY-MM-DD';
const format2 = 'YYYY-MM-DD HH:mm:ss';
class AmlRecord extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            recordData: [],
            current: 1,
            total: 0,
            pageSize: 10,
            isModalVisible: false,
            previewVisible: false,
            previewImage: '',
            postMessagedata: {},
            storeRef: [],
            formRef: '',
            searchParams: getUrlParams(this.props.location.search) || {}
        };
    }

    componentWillReceiveProps (nextProps) { }


    getStore = () => {
        // toGetStoreList().then(res => {
        //     console.log(res)
        //     if (res instanceof Object) {
        //         this.setState({ storeRef: res?.data?.recordList || [] }, () => {
        //             console.log(this.state.storeRef)
        //         })
        //     }
        // })
    }

    componentDidMount () {
        this.setState({ formRef: this.formRef });
        this.getStore()
        this.tosearch();
    }

    onChange = (page, pageSize) => {
        this.setState({ pageSize: pageSize }, () => {
            this.tosearch(page);
        });
    };

    openModal = (cddFormId) => {
        this.setState({ loading: true });
        getamlform(cddFormId).then((res) => {
            this.setState({ loading: false });
            if (res.status === 200) {
                res.data.cddForm.mBirth = res.data.cddForm.mBirth
                    ? moment(res.data.cddForm.mBirth)
                    : '';
                res.data.cddForm.mIdExpiryDate = res.data.cddForm.mIdExpiryDate
                    ? moment(res.data.cddForm.mIdExpiryDate)
                    : '';
                if (res.data.bodForm) {
                    res.data.bodForm.mBirth = res.data.bodForm.mBirth
                        ? moment(res.data.bodForm.mBirth)
                        : '';
                    res.data.bodForm.mIdExpiryDate = res.data.bodForm.mIdExpiryDate
                        ? moment(res.data.bodForm.mIdExpiryDate)
                        : '';
                }
                this.setState({ postMessagedata: res.data }, () => {
                    if (res.data?.cddForm?.finishFill === false) {
                        let param = {
                            outId: cddFormId,
                            sourceType: 'AML_PROFILE',
                        };
                        console.log(this.state.postMessagedata)
                        getAtta(param).then((res) => {
                            console.log(res);
                            if (res.status === 200) {
                                let newarr = []
                                res.data.map(ele => {
                                    let obj = {
                                        name: ele.originalFileName,
                                        status: "done",
                                        thumbUrl: ele.url,
                                        preview: ele.url,
                                        response: {
                                            data: [ele],
                                            status: 200
                                        }
                                    }
                                    newarr.push(obj)
                                })
                                let { postMessagedata } = this.state
                                postMessagedata.sign = {
                                    fileList: newarr
                                }
                                console.log(postMessagedata)
                                this.setState({ postMessagedata }, () => {
                                    this.setState({ isModalVisible: true });
                                });
                            } else {
                                Notification(res.message, 'error');
                            }
                        });
                    } else {
                        this.setState({ isModalVisible: true });
                    }
                });
            }
        });
    };

    tosearch = (page) => {
        let { current, pageSize, searchParams } = this.state;
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                let param = {
                    ...values,
                    ...searchParams,
                    pageIndex: page ? page : current,
                    pageSize: pageSize,
                    docDateBegin: searchParams?.system === 'RETAIL' ? moment().subtract('30', 'days').format(format) : (values?.docDate?.[0] ? moment(values.docDate[0]).format(format) : undefined),
                    docDateEnd: searchParams?.system === 'RETAIL' ? moment().format(format) : (values?.docDate?.[1] ? moment(values.docDate[1]).format(format) : undefined),
                    holder: values.holder ? values.holder.join(',') : undefined
                };
                console.log(param)
                delete param.docDate
                this.setState({ loading: true });
                amlRecord(param).then((res) => {
                    console.log(res);
                    if (res instanceof Object) {
                        this.setState({
                            loading: false,
                            recordData: res.data.records,
                            current: Number(res.data.current),
                            total: res.data.total,
                        });
                    } else {
                        this.setState({ loading: false });
                    }
                });
            })
            .catch((errorInfo) => { });
    };

    formRef = React.createRef();

    toEdit = (id) => {
        this.props.history.push(`/amlDetail?id=${id}`);
    };

    openSign = (url) => {
        this.setState({ previewImage: url }, () => {
            this.setState({ previewVisible: true });
        });
    };

    handleCancel = () => {
        this.setState({ isModalVisible: false, previewVisible: false });
    };

    toClose = () => {
        this.setState({ isModalVisible: false }, () => {
            this.tosearch()
        });
    };

    // 规定不可选的 日期范围
    rangePickerDisabledDate = current => {
        // 不能选今天以后，只能选【今天~往前追溯30天】内，今天可选。
        let result = false;
        if (current) {
            if (current < moment().subtract(30, "days") || current > moment().endOf("day")) {
                result = true;
            } else {
                result = false;
            }
        }
        return result;
    };

    render () {
        let {
            loading,
            recordData,
            current,
            total,
            pageSize,
            isModalVisible,
            postMessagedata,
            previewVisible,
            previewImage,
            storeRef,
            formRef,
            searchParams
        } = this.state;
        const layout = {
            labelCol: {
                span: 8,
            },
            wrapperCol: {
                span: 16,
            },
        };
        const columns = [
            {
                title: 'Outlet',
                dataIndex: 'outletCode',
                width: 80,
                key: 'outletCode',
                // render: (text) => <a>{text}</a>,
            },
            {
                title: 'DocNo.',
                dataIndex: 'orderCode',
                width: 200,
                key: 'orderCode.',
            },
            {
                title: 'DocDate',
                dataIndex: 'orderTime',
                width: 180,
                key: 'orderTime',
                render: (text) => <span>{moment(text).format(format2)}</span>,
            },
            {
                title: 'SalesPerson',
                dataIndex: 'employeeName',
                width: 180,
                key: 'employeeName',
            },
            {
                title: 'TotalAmount',
                dataIndex: 'orderValue',
                width: 120,
                key: 'orderValue',
                align: 'right',
                render: (text) => <span>{text ? Number(text).toFixed(2) : 0}</span>,
            },
            {
                title: 'Description',
                dataIndex: 'orderDesc',
                key: 'orderDesc',
                ellipsis: true,
                width: 300,
                render: (val) => {
                    return (
                        <Tooltip className="noline" overlayClassName='whiteSpace' placement="topLeft" title={val}>
                            {val}
                        </Tooltip>
                    )
                },
            },
            {
                title: 'Full Name',
                width: 180,
                dataIndex: 'memberName',
                key: 'memberName',
            },
            // {
            //     title: 'ID No.',
            //     dataIndex: 'memberIdNumber',
            //     key: 'memberIdNumber',
            //     width: 180,
            // },
            {
                title: 'Nationlity',
                dataIndex: 'memberNationalityName',
                key: 'memberNationalityName',
                width: 180,
            },
            {
                title: 'CTR Uploaded',
                dataIndex: 'ctr',
                key: 'ctr',
                width: 120,
                align: 'center',
                render: (text) => <span>{text ? 'Y' : 'N'}</span>,
            },
            {
                title: 'Complete AML',
                dataIndex: 'finishFill',
                key: 'finishFill',
                align: 'center',
                fixed: 'right',
                width: 120,
                render: (text) => (
                    <span>
                        {text ? "Y" : "N"}
                    </span>
                ),
            },
            {
                title: 'AML',
                dataIndex: 'cddFormId',
                key: 'cddFormId',
                width: 80,
                align: 'center',
                fixed: 'right',
                render: (text) => (
                    <span className="linkicon">
                        <LinkOutlined onClick={() => this.openModal(text)} />
                    </span>
                ),
            },
            {
                title: ' ',
                dataIndex: 'id',
                key: 'id',
                hideInTable: this.state.searchParams.system !== 'RETAIL' ? false : true,
                width: 80,
                fixed: 'right',
                render: (text) => (
                    <Button
                        size="small"
                        className="grayBtn"
                        type="primary"
                        onClick={() => this.toEdit(text)}
                    >
                        Edit
                    </Button>
                ),
            },
        ];
        return (
            <div className="amlRecord">
                <Spin spinning={loading}>
                    <Form
                        {...layout}
                        name="record"
                        size="small"
                        initialValues={{}}
                        ref={this.formRef}
                        colon={false}
                        requiredMark={false}

                    // onFinish={onFinish}
                    // onFinishFailed={onFinishFailed}
                    >
                        <Row>
                            <Col span={7}>
                                <Form.Item label="DocNo." name="docNo" {...layout}>
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="DocDate" name="docDate" {...layout}>
                                    <RangePicker
                                        defaultValue={searchParams?.system === 'RETAIL' ? [moment().subtract(30, "days"), moment()] : null}
                                        disabledDate={searchParams?.system === 'RETAIL' && this.rangePickerDisabledDate}
                                        format={'DD/MM/YYYY'}
                                        allowClear={searchParams?.system === 'RETAIL' ? false : true}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="Outlet" name="holder" {...layout}>
                                    <SelectMultipleCommon
                                        type="holder"
                                        refArr={{ selectData: storeRef, total: storeRef.length }}
                                        formRef={formRef.current}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={3}>
                                <div className="btn">
                                    <Space>
                                        <Button
                                            className="grayBtn"
                                            type="primary"
                                            onClick={() => this.tosearch()}
                                        >
                                            Search
                                        </Button>
                                    </Space>
                                </div>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="Complete AML" name="finishFill" {...layout}>
                                    <Select allowClear>
                                        <Option value={true}>Y</Option>
                                        <Option value={false}>N</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="Use Cash Over 20K" name="cashOver20k" {...layout}>
                                    <Select allowClear>
                                        <Option value={true}>Y</Option>
                                        <Option value={false}>N</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="High Risk Country" name="highRiskNationality" {...layout}>
                                    <Select allowClear>
                                        <Option value={true}>Y</Option>
                                        <Option value={false}>N</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="CTR Uploaded" name="ctrUploaded" {...layout}>
                                    <Select allowClear>
                                        <Option value={true}>Y</Option>
                                        <Option value={false}>N</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                    {/* <Divider /> */}
                    <br />
                    <Table
                        rowKey="id"
                        size="small"
                        columns={columns.filter(item => item.hideInTable !== true)}
                        pagination={false}
                        dataSource={recordData}
                        scroll={{ x: 1800 }}
                    />
                    <div className="pagination">
                        <Pagination
                            defaultCurrent={current}
                            onChange={this.onChange}
                            total={total}
                            pageSize={pageSize}
                            showSizeChanger={true}
                            showQuickJumper
                            showTotal={(total) => `Total ${total} items`}
                        />
                    </div>
                    <Modal
                        visible={previewVisible}
                        footer={null}
                        onCancel={this.handleCancel}
                        width={700}
                        destroyOnClose
                    >
                        <br />
                        <img alt="example" style={{ width: '100%' }} src={previewImage} />
                    </Modal>
                    <Modal
                        title="Details"
                        visible={isModalVisible}
                        onCancel={this.handleCancel}
                        footer={null}
                        width={1000}
                        destroyOnClose
                    >
                        <AntiMoney
                            typename='record'
                            key={JSON.stringify(postMessagedata)}
                            disabled={
                                postMessagedata?.cddForm?.finishFill === false ? false : true
                            }
                            postMessagedata={postMessagedata}
                            closeModal={this.toClose}
                        />
                    </Modal>
                </Spin>
            </div>
        );
    }
}

export default AmlRecord;
