import React, { useState } from 'react'
import URL from '@/api/URL'
import { Spin, Space, Modal, Select, Upload, Button } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import Notification from '@/components/Notification/index'

import './index.less'

const { Option } = Select;

const ImportModal = ({ visible, importType, onOk, onCancel }) => {
    const [isLoading, setIsLoading] = useState(false)
    const [status, setStatus] = useState('')

    const handleOk = () => {

    }

    const props = {
        name: 'files',
        action: `${process.env.REACT_APP_API}/${URL.FRONT2}/${importType === 'Import' ? 'import-excel' : 'import-xml'}`,
        headers: { authorization: 'authorization-text' },
        data: { cautionStatus: status },
        accept: importType === 'Import' ?
            'text/xml,application/xml,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel' :
            'text/xml,application/xml',
        onChange(info) {
            setIsLoading(true)
            if (info.file.status === 'done') {
                console.log(info.file, info.fileList);
                if (info.file.response && info.file.response.status === 200) {
                    setIsLoading(false)
                    Notification(`${info.file.name} file uploaded successfully`, 'success');
                    onOk()
                    onCancel()
                } else {
                    setIsLoading(false)
                    Notification(`${info.file.name}: ${info.file.response.message}`, 'error');
                }
            } else if (info.file.status === 'error') {
                setIsLoading(false)
                Notification(`${info.file.name} file upload failed.`, 'error');
            }
        },
    };

    return (
        <Modal
            className='import-modal'
            title={false}
            visible={visible}
            onCancel={onCancel}
            width={540}
            onOk={handleOk}
            footer={null}
            maskClosable={false}
        >
            <Spin spinning={isLoading} size='small' delay={500}>
                <div className='import-modal-content'>
                    <div>
                        <label>Status:</label>
                        <Select
                            size='small'
                            style={{ width: 240 }}
                            onChange={(val) => setStatus(val)}
                        >
                            <Option value='BLOCK'>Block</Option>
                            <Option value='CAUTION'>Caution</Option>
                        </Select>
                    </div>

                    <div className='action-btn'>
                        <Space>
                            <Upload {...props} showUploadList={false}>
                                <Button
                                    size='small'
                                    disabled={status ? false : true}
                                    icon={<UploadOutlined />}
                                >
                                    {importType}
                                </Button>
                            </Upload>
                            <Button size='small' onClick={onCancel}>
                                Cancel
                            </Button>
                        </Space>
                    </div>
                </div>
            </Spin>

        </Modal>
    );
};


export default ImportModal
