import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Space,
    Spin,
    Table,
    Menu,
    Pagination,
    Dropdown,
    Select,
    Tooltip,
} from 'antd';
import moment from 'moment'
import Notification from '@/components/Notification/index'
import { getCautionList, toChangeStatus, GetAttachments } from '@/api/antiM';
import { DownOutlined, UploadOutlined } from '@ant-design/icons';
import { formatDate } from '@/utils/formatDate'
import ImportModal from './ImportModal';

import './index.less';

const { Option } = Select;
class CautionRecord extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            recordData: [],
            current: 1,
            total: 0,
            pageSize: 10,
            isModalVisible: false,
            editingKey: '',
            visible: false,
            importType: ''
        };
    }

    componentWillReceiveProps(nextProps) { }

    componentDidMount() {
        this.tosearch();
    }

    onChange = (page, pageSize) => {
        this.setState({ pageSize: pageSize }, () => {
            this.tosearch(page);
        });
    };

    tosearch = (page) => {
        let { current, pageSize } = this.state;
        this.formRef.current
            .validateFields()
            .then((values) => {
                // console.log(values);
                let param = {
                    pageIndex: page ? page : current,
                    pageSize: pageSize,
                    name: values.name ? values.name : '',
                    idNo: values.idNo ? values.idNo : '',
                    status: values.status ? values.status : '',
                };
                this.setState({ loading: true });
                getCautionList(param).then((res) => {
                    // console.log(res);
                    if (res?.status === 200) {
                        this.setState({
                            loading: false,
                            recordData: res.data.records,
                            current: res.data.current,
                            total: res.data.total,
                            editingKey: '',
                        });
                    } else {
                        Notification(res.message, 'error');
                        this.setState({ loading: false });
                    }
                });
            })
            .catch((errorInfo) => { });
    };

    formRef = React.createRef();

    tableformRef = React.createRef();

    toEdit = (record) => {
        this.tableformRef.current.setFieldsValue({
            updataStatus: record.status,
        });
        this.setState({ editingKey: record.id });
    };

    isEditing = (record) => {
        let { editingKey } = this.state;
        return record.id === editingKey;
    };

    toCancel = () => {
        this.setState({ editingKey: '' });
    };

    tosave = (record) => {
        let { recordData } = this.state;
        let updataStatus = this.tableformRef.current.getFieldsValue([
            'updataStatus',
        ]).updataStatus;
        console.log(updataStatus);
        this.setState({ loading: true });
        let param = {
            id: record.id,
            status: updataStatus,
        };
        toChangeStatus(param).then((res) => {
            console.log(res);
            if (res.status === 200) {
                recordData.map((ele) => {
                    if (ele.id === record.id) {
                        ele.status = updataStatus;
                        ele.statusValue = updataStatus === 1 ? 'Block' : 'Caution';
                    }
                    return ele
                });
                this.setState({ editingKey: '', loading: false, recordData });
            } else {
                Notification(res.message, 'error');
                this.setState({ loading: false });
            }
        });
    };

    render() {
        let {
            loading,
            recordData,
            current,
            total,
            pageSize,
            // isModalVisible,
            // postMessagedata,
            visible,
            importType,
            editingKey,
        } = this.state;
        const layout = {
            labelCol: {
                span: 8,
            },
            wrapperCol: {
                span: 16,
            },
        };
        const columns = [
            {
                title: 'Sl No',
                dataIndex: 'slNo',
                key: 'slNo',
                fixed: 'left',
            },
            {
                title: 'Name',
                dataIndex: 'name',
                key: 'name.',
                fixed: 'left',
            },
            {
                title: 'ID Type',
                dataIndex: 'idType',
                key: 'idType',
            },
            {
                title: 'ID NO',
                dataIndex: 'idNo',
                key: 'idNo',
            },
            {
                title: 'Nat',
                dataIndex: 'nat',
                key: 'nat',
            },
            {
                title: 'Sex',
                dataIndex: 'sex',
                key: 'sex',
            },
            {
                title: 'Race',
                dataIndex: 'race',
                key: 'race',
            },
            {
                title: 'Birthday',
                dataIndex: 'birth',
                key: 'birth',
                render: (val) => val && moment(val).format(formatDate)
            },
            {
                title: 'Caution Date',
                dataIndex: 'cautionDate',
                key: 'cautionDate',
                render: (val) => val && moment(val).format(formatDate)
            },
            {
                title: 'Created By',
                dataIndex: 'createdBy',
                key: 'createdBy',
            },
            {
                title: 'Source',
                dataIndex: 'sourceValue',
                key: 'sourceValue',
            },
            {
                title: 'Address',
                dataIndex: 'address',
                key: 'address',
                width: 200,
                render: (text) => (
                    <Tooltip className="noline" placement="topLeft" title={text}>
                        {text}
                    </Tooltip>
                ),
            },
            {
                title: 'Remarks',
                dataIndex: 'remarks',
                key: 'remarks',
                width: 200,
                render: (text) => (
                    <Tooltip className="noline" placement="topLeft" title={text}>
                        {text}
                    </Tooltip>
                ),
                // width:200
            },
            {
                title: 'Status',
                dataIndex: 'statusValue',
                key: 'statusValue',
                fixed: 'right',
                render: (text, record) => {
                    const editable = this.isEditing(record);
                    return editable ? (
                        <Form.Item name="updataStatus" className="editForm">
                            <Select size='small'>
                                <Option value={1}>Block</Option>
                                <Option value={2}>Caution</Option>
                            </Select>
                        </Form.Item>
                    ) : (
                        text
                    );
                },
            },
            {
                title: 'Action',
                dataIndex: 'id',
                key: 'id',
                fixed: 'right',
                render: (text, record) => {
                    const editable = this.isEditing(record);
                    return editable ? (
                        <span>
                            <Button
                                size='small'
                                className="grayBtn"
                                type="primary"
                                onClick={() => this.tosave(record)}
                                style={{
                                    marginRight: 8,
                                }}
                            >
                                Save
                            </Button>
                            <Button
                                // className="grayBtn"
                                // type="primary"
                                onClick={this.toCancel}
                            >
                                Cancel
                            </Button>
                            {/* <Typography.Link
                onClick={() => this.tosave(record)}
                style={{
                  marginRight: 8,
                }}
              >
                Save
              </Typography.Link>
              <Typography.Link onClick={this.toCancel}>Cancel</Typography.Link> */}
                        </span>
                    ) : (
                        <Button
                            size='small'
                            className={editingKey !== '' ? '' : 'grayBtn'}
                            type="primary"
                            onClick={() => this.toEdit(record)}
                            disabled={editingKey !== ''}
                        >
                            Edit
                        </Button>
                        // <Typography.Link
                        //   disabled={editingKey !== ''}
                        //   onClick={() => this.toEdit(record)}
                        // >
                        //   Edit
                        // </Typography.Link>
                    );
                },
            },
        ];

        const menu = (
            <Menu
                items={[{
                    key: '1',
                    label: 'Import',
                    onClick: () => this.setState({ visible: true, importType: 'Import' })
                }, {
                    key: '3',
                    label: 'Download template',
                    onClick: () => {
                        GetAttachments({
                            sourceType: 'CAUTION_LIST_IMPORT_EXCEL_TEMPLATE'
                        }).then(res => {
                            if (res instanceof Object) {
                                if (res.data) {
                                    openDownloadDialog(res.data)
                                    Notification(res.message, 'success')
                                } else {
                                    Notification('No template, please contact the administrator', 'info')
                                }
                            }
                        })
                    }
                }]}
            />
        );

        return (
            <div className="cautionRecord">
                <Spin spinning={loading}>
                    <Form
                        {...layout}
                        name="record"
                        initialValues={{}}
                        ref={this.formRef}
                        colon={false}
                        requiredMark={false}
                        size='small'
                    >
                        <Row>
                            <Col span={7}>
                                <Form.Item label="Name" name="name" {...layout}>
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="ID NO." name="idNo" {...layout}>
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col span={7}>
                                <Form.Item label="Status" name="status" {...layout}>
                                    <Select allowClear>
                                        <Option value={1}>Block</Option>
                                        <Option value={2}>Caution</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={3}>
                                <div className="btn">
                                    <Space>
                                        <Button
                                            className="grayBtn"
                                            type="primary"
                                            onClick={() => this.tosearch(1)}
                                        >
                                            Search
                                        </Button>
                                    </Space>
                                </div>
                            </Col>
                            <Col span={21}></Col>
                            <Col span={3}>
                                <div className="btn">
                                    <Dropdown overlay={menu}>
                                        <Button>
                                            <Space>
                                                Import Excel
                                                <DownOutlined />
                                            </Space>
                                        </Button>
                                    </Dropdown>
                                    <Button
                                        icon={<UploadOutlined />}
                                        onClick={() => this.setState({ visible: true, importType: 'Import XML' })}
                                    >
                                        Import XML
                                    </Button>
                                </div>
                            </Col>
                        </Row>
                    </Form>
                    {/* <Divider /> */}
                    {/* <br /> */}
                    <Form ref={this.tableformRef} component={false}>
                        <Table
                            size="small"
                            rowKey='id'
                            columns={columns}
                            pagination={false}
                            dataSource={recordData}
                            scroll={{ x: 'max-content' }}
                        />
                    </Form>
                    <div className="pagination">
                        <Pagination
                            size='small'
                            defaultCurrent={current}
                            onChange={this.onChange}
                            total={total}
                            pageSize={pageSize}
                            showSizeChanger={true}
                            showQuickJumper
                            showTotal={(total) => `Total ${total} items`}
                        />
                    </div>
                </Spin>
                {
                    visible &&
                    <ImportModal
                        visible={visible}
                        importType={importType}
                        onOk={() => this.tosearch(1)}
                        onCancel={() => this.setState({ visible: false })}
                    />
                }
            </div>
        );
    }
}


// 打开下载
function openDownloadDialog(data) {
    let link = document.createElement('a')
    link.style.display = 'none'
    link.href = data[0].url
    // 触发点击方法
    link.setAttribute('download', data[0].originalFileName)
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link);

}

export default CautionRecord;
