import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Divider,
    Radio,
    Checkbox,
    Space,
    Typography,
} from 'antd';
import './index.less';
import moment from 'moment';
import { formatDate_hh_mm_ss, } from '@/utils/formatDate';
const { Paragraph } = Typography;

class AntiMoneyFormE extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            postMessagedata: this.props.postMessagedata,
            checkref: this.props.checkref,
            formdata: this.props.formdata,
        };
    }

    componentDidMount() {
        console.log(this.props);
        // this.props.formB(this);
    }

    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        if (
            JSON.stringify(nextProps.postMessagedata) !== this.props.postMessagedata
        ) {
            this.setState({ postMessagedata: nextProps.postMessagedata }, () => {
                this.formRef.current.resetFields();
            });
        }
        if (JSON.stringify(nextProps.checkref) !== this.props.checkref) {
            this.setState({ checkref: nextProps.checkref }, () => { });
        }
        if (JSON.stringify(nextProps.formdata) !== this.props.formdata) {
            this.setState({ formdata: nextProps.formdata }, () => {
                this.formRef.current.resetFields();
            });
        }
    }

    clearForm = (value, lastcode, formname) => {
        console.log(value);
        if (!value.includes(lastcode)) {
            this.formRef.current.setFieldsValue({
                [formname]: '',
            });
        }
    };

    jumpNext = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formE');
                this.props.changeShowtype('formCate');
            })
            .catch((errorInfo) => { });
    };

    tosave = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formE', true);
            })
            .catch((errorInfo) => { });
    };

    goback = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formE');
                this.props.changeShowtype('formB');
            })
            .catch((errorInfo) => {
                this.props.saveFormData(errorInfo.values, 'formE');
                this.props.changeShowtype('formB');
            });
    };

    formRef = React.createRef();
  // jumpNext = () => {
  //   this.formRef.current
  //     .validateFields()
  //     .then((values) => {
  //       console.log(values);
  //       this.props.saveFormData(values, 'formE');
  //       this.props.changeShowtype('formCate');
  //     })
  //     .catch((errorInfo) => {});
  // };

    jumpNext = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                this.props.saveFormData(values, 'formE');
                this.props.changeShowtype('formCate');
            })
            .catch((errorInfo) => { });
    };

    toClose = () => {
        let postMessageData = {
            key: 'close',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

    render() {
        const { postMessagedata, checkref, formdata } = this.state;
        const layout = {
            labelCol: {
                span: 10,
            },
            wrapperCol: {
                span: 14,
            },
        };
        const layout2 = {
            labelCol: {
                span: 5,
            },
            wrapperCol: {
                span: 19,
            },
        };
        const layout3 = {
            labelCol: {
                span: 24,
            },
            wrapperCol: {
                span: 24,
            },
        };
        const ruleConfig = {
            rules: [{ required: true, message: 'Please enter required fields' }],
        };
        const ruleConfigselect = {
            rules: [{ required: true, message: 'Please select required fields' }],
        };
        const disabledConfig = {
            disabled: formdata.disabled ? formdata.disabled : false,
        };
        let initialValuesConfig =
            formdata && formdata.formE
                ? {
                    ...formdata.formE
                }
                : {
                    outletCode: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch.cStoreCode
                        : '',
                    cddDate: postMessagedata.data
                        ? moment(postMessagedata.data.rm_retailvouch.createTime).format(formatDate_hh_mm_ss)
                        : '',
                    employeeName: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch
                            .retailVouchHeaderExtend_Employeename
                        : '',
                    roName: postMessagedata.formSMS
                        ? postMessagedata.formSMS.areamanagerName
                        : '',
              orderId: postMessagedata?.formData?.id || '',
                };
        return (
          <div className="antiMoneyformE">
            <Form
              {...layout}
              name="formE"
              initialValues={{
                ...initialValuesConfig,
              }}
              ref={this.formRef}
              colon={false}
              requiredMark={false}
            >
              <Row>
                <Col span={24}>
                  <div className="title">
                    Customer Due Diligence(CDD)-ECCD FORM
                  </div>
                </Col>
                <Col span={12}>
                  <Form.Item
                    // {...ruleConfig}
                    label="Record No."
                    name="orderId"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Outlet" name="outletCode">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Date" name="cddDate">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Name of Employee"
                    name="employeeName"
                    {...layout2}
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Divider />
                <div className="title">
                  A. Income Level, Source of Wealth and Source of Funds
                </div>
                <Col span={24}>
                  <Typography>
                    <Paragraph>
                      Establish (by reasonable means e.g. internet scanning,
                      careful enquiries with the customer, etc.) the following:
                    </Paragraph>
                    <Paragraph>
                      a. income level;Below 50K，50-150K，$150-$250K，{'>'}$250K
                    </Paragraph>
                    <Paragraph>
                      b. source of wealth (i.e. source of overall assets); and
                    </Paragraph>
                    <Paragraph>
                      c. source of funds (i.e. source of the funds used to pay
                      for the transaction)
                    </Paragraph>
                    <br />
                  </Typography>
                </Col>
                <Col span={12} className="leftLable">
                  <Form.Item
                    {...ruleConfigselect}
                    label="Annual Income"
                    name="annualIncome"
                    {...layout3}
                  >
                    <Radio.Group {...disabledConfig}>
                      <Space direction="vertical">
                        {checkref.annual_income
                          ? checkref.annual_income.map((ele) => {
                              return <Radio value={ele.code}>{ele.name}</Radio>;
                            })
                          : ''}
                      </Space>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Col span={12} className="leftLable">
                  <Form.Item
                    {...ruleConfigselect}
                    label="Source of wealth"
                    name="wealthSource"
                    {...layout3}
                  >
                    <Checkbox.Group
                      {...disabledConfig}
                      onChange={(value) =>
                        this.clearForm(
                          value,
                          checkref.wealth_source[
                            checkref.wealth_source.length - 1
                          ].code,
                          'wealthSourceOther'
                        )
                      }
                    >
                      <Space direction="vertical">
                        {checkref.wealth_source
                          ? checkref.wealth_source.map((ele, index) => {
                              if (index === checkref.wealth_source.length - 1) {
                                return (
                                  <Checkbox value={ele.code}>
                                    {ele.name}{' '}
                                    <Form.Item
                                      {...ruleConfig}
                                      dependencies={['wealthSource']}
                                      name="wealthSourceOther"
                                      className="inlineForm"
                                      rules={[
                                        ({ getFieldValue }) => ({
                                          validator(_, value) {
                                            console.log(value);
                                            if (
                                              getFieldValue('wealthSource') &&
                                              getFieldValue(
                                                'wealthSource'
                                              ).includes('OTHERS') &&
                                              !getFieldValue(
                                                'wealthSourceOther'
                                              )
                                            ) {
                                              return Promise.reject(
                                                new Error(
                                                  'Please enter required fields'
                                                )
                                              );
                                            }
                                            return Promise.resolve();
                                          },
                                        }),
                                      ]}
                                    >
                                      <Input {...disabledConfig} />
                                    </Form.Item>
                                  </Checkbox>
                                );
                              } else {
                                return (
                                  <Checkbox value={ele.code}>
                                    {ele.name}
                                  </Checkbox>
                                );
                              }
                            })
                          : ''}
                      </Space>
                    </Checkbox.Group>
                  </Form.Item>
                </Col>
                <Col span={12} className="leftLable">
                  <Form.Item
                    {...ruleConfigselect}
                    label="Source of funds"
                    name="fundsSource"
                    {...layout3}
                  >
                    <Checkbox.Group {...disabledConfig}>
                      <Space direction="vertical">
                        {checkref.funds_source
                          ? checkref.funds_source.map((ele) => {
                              return (
                                <Checkbox value={ele.code}>{ele.name}</Checkbox>
                              );
                            })
                          : ''}
                      </Space>
                    </Checkbox.Group>
                  </Form.Item>
                </Col>
                <Col span={12} className="leftLable">
                  <Form.Item
                    {...ruleConfigselect}
                    label="Source of information"
                    name="infoSource"
                    {...layout3}
                  >
                    <Checkbox.Group
                      {...disabledConfig}
                      onChange={(value) =>
                        this.clearForm(
                          value,
                          checkref.info_source[checkref.info_source.length - 1]
                            .code,
                          'infoSourceOther'
                        )
                      }
                    >
                      <Space direction="vertical">
                        {checkref.info_source
                          ? checkref.info_source.map((ele, index) => {
                              if (index === checkref.info_source.length - 1) {
                                return (
                                  <Checkbox value={ele.code}>
                                    {ele.name}{' '}
                                    <Form.Item
                                      // {...ruleConfig}
                                      dependencies={['infoSource']}
                                      name="infoSourceOther"
                                      className="inlineForm"
                                      rules={[
                                        ({ getFieldValue }) => ({
                                          validator(_, value) {
                                            console.log(value);
                                            if (
                                              getFieldValue('infoSource') &&
                                              getFieldValue(
                                                'infoSource'
                                              ).includes('OTHERS') &&
                                              !getFieldValue('infoSourceOther')
                                            ) {
                                              return Promise.reject(
                                                new Error(
                                                  'Please enter required fields'
                                                )
                                              );
                                            }
                                            return Promise.resolve();
                                          },
                                        }),
                                      ]}
                                    >
                                      <Input {...disabledConfig} />
                                    </Form.Item>
                                  </Checkbox>
                                );
                              } else {
                                return (
                                  <Checkbox value={ele.code}>
                                    {ele.name}
                                  </Checkbox>
                                );
                              }
                            })
                          : ''}
                      </Space>
                    </Checkbox.Group>
                  </Form.Item>
                </Col>
                <Paragraph>
                  * Important:To consider terminating the transaction if unable
                  to establish source of income and lodge STR.
                </Paragraph>
                <Divider />
                <div className="title">
                  B. Prior Approval from an Employee of Senior
                  Managerial/Executive Position
                </div>
                <Typography>
                  <Paragraph>
                    Obtain prior approval from an employee holding a senior
                    managerial or executive position before
                    establishing/continuing dealings with a higher-risk
                    customer.
                  </Paragraph>
                  <Paragraph>
                    Note：Terminate POS transaction if approval not given
                  </Paragraph>
                  <br />
                </Typography>
                <Col span={12}>
                  <Form.Item disabled label="RO Name" name="roName">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Divider />
              </Row>
            </Form>
            <div className="btn">
              {this.props.printnow ? (
                ''
              ) : (
                <Space>
                  {formdata.disabled === undefined && (
                    <Button onClick={this.toClose}>Close</Button>
                  )}
                  <Button onClick={this.goback}>Back</Button>
                  {!formdata.disabled ? (
                    // <Button onClick={this.tosave}>Save</Button>
                    <Button onClick={this.jumpNext}>Next</Button>
                  ) : (
                    <Button onClick={this.jumpNext}>Next</Button>
                  )}
                </Space>
              )}
            </div>
          </div>
        );
    }
}

export default AntiMoneyFormE;
