import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Divider,
    Space,
    Statistic,
    Select,
    Spin,
    Modal,
} from 'antd';
import './index.less';
import { getManager, sendSMS, verifySms, saveAnti } from '@/api/antiM';
import { getUrlParams } from '@/utils/getUrl';
import moment from 'moment';
import Notification from '@/components/Notification/index';
import testdata from '../antiMoney/test';
import test from './test';
import { formatDate, formatDate_hh_mm_ss } from '@/utils/formatDate';

const { Countdown } = Statistic;
const { Option } = Select;

class VerificationSMS extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            countdown: false,
            postMessagedata: {},
            mamagerRef: [],
            selectMa: {},
            isModalVisible: false,
            step: 1,
            paramAML: {},
        };
    }

    componentWillReceiveProps (nextProps) { }

    componentWillMount () {
        let postMessageData = {
            key: 'ok',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    }

    componentDidMount () {
        let url = window.location.href;
        let params = getUrlParams(url);
        console.log(params);
        window.addEventListener('message', (event) => {
            console.log(event);
            if (event.data && Object.prototype.toString.call(event.data) == "[object String]") {
                let eventData = JSON.parse(event.data);
                if (eventData.key === params.cguid) {
                    let param = {
                        storeId: eventData.data.rm_retailvouch.store,
                        yht_access_token: eventData.yht_access_token,
                        yhtTenantId: eventData.yhtTenantId,
                    };
                    this.setState({ loading: true, postMessagedata: eventData });
                    if (
                        eventData &&
                        eventData.checkData &&
                        eventData.checkData.block === false &&
                        eventData.checkData.caution === false
                    ) {
                        this.setState({ step: 2 });
                        // if (eventData.checkData.roMessage) {
                        //   this.setState({ step: 2 });
                        // } else {
                        //   this.toSms();
                        // }
                    }
                    getManager(param).then((res) => {
                        if (res.status === 200) {
                            this.setState({
                                loading: false,
                                mamagerRef: res.data,
                            });
                        } else {
                            this.setState({ loading: false });
                        }
                    });
                }
            }
            // console.log(testdata.key);
            // if (testdata.key === params.cguid) {
            // this.setState({ postMessagedata: testdata, mamagerRef: test.data });
            // }
        });
    }

    jumpNext = () => {
        let { postMessagedata, selectMa } = this.state;
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
                let param = {
                    phone: values.phone,
                    otp: values.otp,
                };
                this.setState({ loading: true });
                verifySms(param).then((res) => {
                    this.setState({ loading: false });
                    if (res?.status === 200) {
                        let param = {
                            pathname: '/antiMoney',
                            query: {
                                postMessagedata: postMessagedata,
                                formSMS: {
                                    areamanagerId: values.areamanagerId,
                                    areamanagerName: selectMa.children,
                                    aPhoneNum: values.phone,
                                    aVerificationCode: values.otp,
                                },
                            },
                        };
                        // this.setState({ isModalVisible: true, paramAML: param });
                        this.props.history.push({
                            pathname: '/antiMoney',
                            query: {
                                postMessagedata: postMessagedata,
                                formSMS: {
                                    areamanagerId: values.areamanagerId,
                                    areamanagerName: selectMa.children,
                                    aPhoneNum: values.phone,
                                    aVerificationCode: values.otp,
                                },
                            },
                        });
                    } else {
                        this.setState({ loading: false });
                        Notification(res.message, 'error');
                    }
                });
            })
            .catch((errorInfo) => {
                this.setState({ loading: false });
            });
    };

    formRef = React.createRef();

    onFinish = () => {
        this.setState({ countdown: false });
    };

    sendSMS = () => {
        let phone = this.formRef.current.getFieldValue('phone');
        console.log(phone);
        if (phone) {
            this.setState({ countdown: true });
            sendSMS(phone).then((res) => {
                console.log(res);
            });
        } else {
            Notification('Please select Area Manager', 'error');
        }
    };

    toClose = () => {
        let postMessageData = {
            key: 'close',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

    toSettlement = () => {
        let postMessageData = {
            key: 'finish',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

    getPhone = (value, option) => {
        console.log(value, option);
        this.formRef.current.setFieldsValue({
            phone: option.phone,
        });
        this.setState({ selectMa: option });
    };

    modalBtn = () => {
        this.setState({ isModalVisible: false });
    };

    toSms = () => {
        let { postMessagedata } = this.state;
        if (!postMessagedata.checkData.roMessage) {
            let param = {
                pathname: '/antiMoney',
                query: {
                    postMessagedata: postMessagedata,
                    formSMS: {},
                },
            };
            // this.setState({ isModalVisible: true, paramAML: param });
            this.props.history.push({
                pathname: '/antiMoney',
                query: {
                    postMessagedata: postMessagedata,
                    formSMS: {},
                },
            });
        } else {
            this.setState({ step: 2 });
        }
    };

    toJumpAml = () => {
        let { paramAML } = this.state;
        this.props.history.push({
            ...paramAML,
        });
    };

    getDesc = (data) => {
        let resule = [];
        data.retailVouchDetails.map((ele, index) => {
            resule.push(
                index + 1 + '：' + ele.productsku_cCode + ' ' + ele.product_cName
            );
            return false;
        });
        return resule.join('\n');
    };

    // 暂存
    toTemporary = () => {
        let { postMessagedata, paramAML } = this.state;
        let param = {
            profileReport: postMessagedata?.checkData?.profileReport
                ? postMessagedata.checkData.profileReport
                : '',
            cddFormDto: {
                ...paramAML.query.formSMS,
                businesstypeId: postMessagedata.formData
                    ? postMessagedata.formData.iBusinesstypeid
                    : '',
                businesstypeName: postMessagedata.formData
                    ? postMessagedata.formData.iBusinesstypeid_name
                    : '',
                outletCode: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch.cStoreCode
                    : '',
                outletId: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch.store
                    : '',
                cddDate: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch.createTime
                    : '',
                employeeName: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch
                        .retailVouchHeaderExtend_Employeename
                    : '',
                employeeId: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch
                        .retailVouchHeaderExtend_Employeeid
                    : '',
                txnTime: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch.createTime
                    : '',
                txnValue: postMessagedata.data
                    ? postMessagedata.data.rm_retailvouch.fGatheringMoney
                        ? Number(
                            postMessagedata.data.rm_retailvouch.fGatheringMoney
                        ).toFixed(2)
                        : 0
                    : '',
                txnDesc: postMessagedata.data
                    ? this.getDesc(postMessagedata.data.rm_retailvouch)
                    : '',
                mName: postMessagedata.memberInfo
                    ? postMessagedata.memberInfo.data.realname
                    : '',
                mBirth: postMessagedata.memberInfo
                    ? postMessagedata.memberInfo.data.birthday
                    : '',
                mPhone: postMessagedata.memberInfo
                    ? postMessagedata.memberInfo.data.phone
                    : '',
                mNationalityId: postMessagedata.memberInfo
                    ? postMessagedata.memberInfo.data.define1
                    : '',
                terrorist: postMessagedata.checkData
                    ? postMessagedata.checkData.terrorist
                    : '',
                sanction: postMessagedata?.checkData?.sanction
                    ? postMessagedata.checkData.sanction
                    : '',
                pep: postMessagedata.checkData ? postMessagedata.checkData.pep : '',
                fatf: postMessagedata.checkData ? postMessagedata.checkData.fatf : '',
                highRiskCustomer: postMessagedata.checkData
                    ? postMessagedata.checkData.highRiskCustomer
                    : '',
                finishFill: false,
            },
        };
        console.log(param);
        this.setState({ loading: true });
        saveAnti(param).then((res) => {
            console.log(res);
            this.setState({ loading: false });
            if (res.status === 200) {
                let postMessageData = {
                    key: 'finish',
                    id: res.data.id,
                };
                window.parent.postMessage(JSON.stringify(postMessageData), '*');
                // this.setState({ isModalVisible: false });
            } else {
                if (res.message) Notification(res.message, 'error');
            }
        });
    };

    render () {
        let {
            isModalVisible,
            loading,
            countdown,
            mamagerRef,
            postMessagedata,
            step,
        } = this.state;
        let { checkData } = postMessagedata;
        console.log(postMessagedata);
        const layout = {
            labelCol: {
                span: 10,
            },
            wrapperCol: {
                span: 14,
            },
        };
        const layout2 = {
            labelCol: {
                span: 5,
            },
            wrapperCol: {
                span: 12,
            },
        };
        const ruleConfig = {
            rules: [{ required: true, message: 'Please enter required fields' }],
        };
        // const ruleConfigselect = {
        //     rules: [{ required: true, message: 'Please select required fields' }],
        // };
        return (
            <div className="verificationSMS">
                <Spin spinning={loading}>
                    {step === 2 ? (
                        <span>
                            <Form
                                {...layout}
                                name="sms"
                                initialValues={{}}
                                ref={this.formRef}
                                colon={false}
                                requiredMark={false}
                            // onFinish={onFinish}
                            // onFinishFailed={onFinishFailed}
                            >
                                <Row>
                                    <Col span={24}>
                                        <div className="title">
                                            <p>There is matches with</p>
                                            <p>PEP Count : {checkData ? checkData.pepCount : ''}</p>
                                            <p>SIP Count : {checkData ? checkData.sipCount : ''}</p>
                                            <p>
                                                for this customer, Please proceed to fill up the{' '}
                                                {checkData && checkData.ecdd ? 'ECDD' : 'CDD'}{checkData?.cashOver20k === true ? ' and CTR' : ''}
                                                {' form.'}
                                            </p>
                                            <Row>
                                                <Col span={12}>
                                                    {' '}
                                                    <p>Member:{checkData ? checkData.name : ''} </p>{' '}
                                                </Col>
                                                <Col span={12}>
                                                    {' '}
                                                    <p>
                                                        Nationality:
                                                        {checkData ? checkData.nationalityName : ''}
                                                        <span className="redcolorsty">
                                                            {checkData && checkData.fatf
                                                                ? '(High Risk Country)'
                                                                : ''}
                                                        </span>{' '}
                                                    </p>{' '}
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col span={12}>
                                                    {' '}
                                                    <p>
                                                        DOB:
                                                        {checkData
                                                            ? moment(checkData.birthday, 'YYYY-MM-DD').format(
                                                                'DD/MM/YYYY'
                                                            )
                                                            : ''}
                                                    </p>{' '}
                                                </Col>
                                                <Col span={12}>
                                                    {' '}
                                                    <p>
                                                        {' '}
                                                        Message:{checkData ? checkData.message : ''}{' '}
                                                    </p>{' '}
                                                </Col>
                                            </Row>
                                        </div>
                                    </Col>
                                    <Divider />
                                    {checkData.roMessage ? (
                                        <>
                                            <Col offset={4} span={20}>
                                                <Form.Item
                                                    {...ruleConfig}
                                                    label="Area Manager"
                                                    name="areamanagerId"
                                                    {...layout2}
                                                >
                                                    <Select onChange={this.getPhone}>
                                                        {mamagerRef.map((ele) => {
                                                            return (
                                                                <Option value={ele.id} phone={ele.cPhone}>
                                                                    {ele.name}
                                                                </Option>
                                                            );
                                                        })}
                                                    </Select>
                                                </Form.Item>
                                            </Col>
                                            <Col offset={4} span={20}>
                                                <Form.Item
                                                    {...ruleConfig}
                                                    label="Phone Num."
                                                    name="phone"
                                                    {...layout2}
                                                >
                                                    <Input disabled />
                                                </Form.Item>
                                            </Col>
                                            <Col offset={4} span={20}>
                                                <Form.Item
                                                    {...ruleConfig}
                                                    label="Verification Code"
                                                    name="otp"
                                                    {...layout2}
                                                >
                                                    <Input
                                                        suffix={
                                                            <span className="sendSMS">
                                                                {!countdown ? (
                                                                    <span onClick={this.sendSMS}>
                                                                        Send verification Code
                                                                    </span>
                                                                ) : (
                                                                    <span className="coutdown">
                                                                        It can be retransmitted in&nbsp;
                                                                        <Countdown
                                                                            title=""
                                                                            value={Date.now() + 61000}
                                                                            onFinish={this.onFinish}
                                                                            format="ss"
                                                                            valueStyle={{
                                                                                fontSize: 14,
                                                                                color: '#ff4d4f',
                                                                            }}
                                                                        />
                                                                        &nbsp;seconds
                                                                    </span>
                                                                )}
                                                            </span>
                                                        }
                                                    />
                                                </Form.Item>
                                            </Col>
                                            <Divider />
                                        </>
                                    ) : (
                                        ''
                                    )}
                                </Row>
                            </Form>
                            <div className={checkData.roMessage ? 'btn' : 'toBottomBtn'}>
                                <Space>
                                    <Button onClick={this.toClose}>Close</Button>
                                    {checkData.roMessage ? (
                                        <Button onClick={this.jumpNext}>Next</Button>
                                    ) : (
                                        <Button onClick={this.toSms}>Next</Button>
                                    )}
                                </Space>
                            </div>
                        </span>
                    ) : (
                        <span>
                            {postMessagedata &&
                                postMessagedata.checkData &&
                                postMessagedata.checkData.caution ? (
                                <span className="title">
                                    <p>
                                        The member is in the caution list and the status is Caution!
                                    </p>
                                    <br />
                                    <p>Caution Date:{postMessagedata.checkData.cautionDate}</p>
                                    <br />
                                    <p>Remarks:{postMessagedata.checkData.remarks}</p>
                                    <br />
                                    {postMessagedata.checkData.ecdd === false &&
                                        postMessagedata.checkData.cdd === false ? (
                                        <Space className="toBottomBtn">
                                            <Button onClick={this.toClose}>Close</Button>
                                            <Button onClick={this.toSettlement}>Next</Button>
                                        </Space>
                                    ) : (
                                        <Space className="toBottomBtn">
                                            <Button onClick={this.toClose}>Close</Button>
                                            <Button onClick={this.toSms}>Next</Button>
                                        </Space>
                                    )}
                                </span>
                            ) : (
                                ''
                            )}
                            {postMessagedata &&
                                postMessagedata.checkData &&
                                postMessagedata.checkData.block ? (
                                <span className="title">
                                    <p>
                                        The member is in the caution list and the status is Block!
                                    </p>
                                    <br />
                                    <p>Caution Date:{postMessagedata.checkData.cautionDate}</p>
                                    <br />
                                    <p>Remarks:{postMessagedata.checkData.remarks}</p>
                                    <br />
                                    <Button className="toBottomBtn" onClick={this.toClose}>
                                        Close
                                    </Button>
                                </span>
                            ) : (
                                ''
                            )}
                        </span>
                    )}
                    <Modal
                        title="Message"
                        visible={isModalVisible}
                        footer={false}
                        // okText={'Complete the AML'}
                        // cancelText={'Confirm'}
                        onCancel={() => {
                            this.setState({ isModalVisible: false });
                        }}
                        destroyOnClose
                    >
                        <p>
                            Save CDD for later, proceed to settle bill?
                        </p>
                        <Divider />
                        <div className="btnGroupAml">
                            <Space>
                                <Button onClick={this.toTemporary}>Settle</Button>
                                <Button type="primary" onClick={this.toJumpAml}>
                                    Complete CDD
                                </Button>
                            </Space>
                        </div>
                    </Modal>
                </Spin>
            </div>
        );
    }
}

export default VerificationSMS;
