import React from 'react';
import {
    Button,
    Row,
    Col,
    Space,
    Modal,
    Image,
    Divider,
    Upload,
    Spin,
} from 'antd';
import './index.less';
import AntiMoneyFormSign from '../formSign';
import { UPsign } from '@/api/antiM';
import { EditOutlined, UploadOutlined } from '@ant-design/icons';
import Notification from '@/components/Notification/index'
import URL from '@/api/URL'

import ConvertImage from '@/utils/ConvertImage.js';
import ImageCompressor from '@/utils/ImageCompressor.js';

class AntiMoneyFormCertificate extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            formdata: this.props.formdata,
            isModalVisible: false,
            imgUrl: null,
            imgId: '',
            fileList: [],
            previewVisible: false,
            previewImage: '',
        };
    }

    handleCancel = () => this.setState({ previewVisible: false });

    componentWillReceiveProps (nextProps) {
        console.log(nextProps.formdata);
        if (JSON.stringify(nextProps.formdata) !== this.props.formdata) {
            this.setState({ formdata: nextProps.formdata }, () => {
                if (nextProps.formdata.formCate) {
                    this.setState({
                        imgId: nextProps.formdata.formCate.imgId,
                        imgUrl: nextProps.formdata.formCate.imgUrl,
                        fileList: nextProps?.formdata?.formCate?.fileList || [],
                    });
                }
            });
        }
    }

    getBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = (error) => reject(error);
        });
    };

    componentDidMount () {
        console.log(this.props);
        if (this.props.formdata.formCate) {
            this.setState({
                imgId: this.props.formdata.formCate.imgId,
                imgUrl: this.props.formdata.formCate.imgUrl,
                fileList: this.props?.formdata?.formCate?.fileList || [],
            }, () => {
              console.log(this.state.fileList)
            });
        }
        // this.browserRedirect();
    }

    browserRedirect = () => {
        var sUserAgent = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i) === 'ipad';
        var bIsIphoneOs = sUserAgent.match(/iphone os/i) === 'iphone os';
        var bIsMidp = sUserAgent.match(/midp/i) === 'midp';
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) === 'rv:1.2.3.4';
        var bIsUc = sUserAgent.match(/ucweb/i) === 'ucweb';
        var bIsAndroid = sUserAgent.match(/android/i) === 'android';
        var bIsCE = sUserAgent.match(/windows ce/i) === 'windows ce';
        var bIsWM = sUserAgent.match(/windows mobile/i) === 'windows mobile';
        if (
            !(
                bIsIpad ||
                bIsIphoneOs ||
                bIsMidp ||
                bIsUc7 ||
                bIsUc ||
                bIsAndroid ||
                bIsCE ||
                bIsWM
            )
        ) {
            console.log('pc');
        } else {
            console.log('yidong');
        }
    };

    goback = () => {
        let { formdata, imgUrl, fileList, imgId } = this.state;
        console.log(formdata);
        let values = {
            imgUrl: imgUrl,
            fileList: fileList,
            imgId: imgId,
            signBase64: this?.props?.formdata?.formCate?.signBase64 || ''
        };
        this.props.saveFormData(values, 'formCate');
        if (formdata.formE) {
            this.props.changeShowtype('formE');
        } else {
            this.props.changeShowtype('formB');
        }
    };

    tosave = () => {
        let { formdata, imgUrl, fileList, imgId } = this.state;
        if (["NRIC", "FIN"].includes(formdata?.formA?.mIdType) && fileList.length < 2) {
            Notification('Upload Front and Back of ID.');
            return;
        }

        if (["PASSPORT"].includes(formdata?.formA?.mIdType) && fileList.length < 1) {
            Notification('Upload passport info.');
            return;
        }

        if (["OTHERS"].includes(formdata?.formA?.mIdType) && fileList.length < 1) {
            Notification('Upload front and back of ID (where applicable.');
            return;
        }
      if (fileList.length === 0) {
        Notification('Please upload IC Images', 'error');
        return;
      }

        // console.log(formdata);
        // let flag = false;
        // fileList.map((ele) => {
        //     if (ele.response.status === 200) {
        //         flag = true;
        //     }
        //     return false
        // });
        // if (!flag) {
        //     Notification('Please upload IC Images', 'error');
        //     return;
        // }
        if (!imgUrl) {
            Notification('Sales Staff Signature, please', 'error');
            return;
        }
        let values = {
            imgUrl: imgUrl,
            fileList: fileList,
            imgId: imgId,
        };
        console.log(values)
      this.props.saveFormData(values, 'formCate', true, 'save');//settle走暂存，save走保存
    };

    handlePreview = async (file) => {
        if (!file.url && !file.preview) {
            file.preview = await this.getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleOk = () => {
        this.Child.trim();
        this.setState({ isModalVisible: false });
    };

    openSignModal = () => {
        this.setState({ isModalVisible: true });
    };

    getImgUrl = (imgUrl) => {
        let { formdata } = this.state
        let { editPostMessagedata } = this.props
        this.setState({ imgUrl: imgUrl }, () => {
            this.setState({ loading: true });
            let param = {
                imgurl: [imgUrl],
            };
            if (formdata.disabled === false) {
                param.outId = editPostMessagedata?.cddForm?.id || '';
            }
            console.log('1111')
            console.log(param)
            UPsign(param).then((res) => {
                console.log(res);
                if (res.status === 200) {
                    this.setState({ loading: false, imgId: res.data[0].id });
                } else {
                    this.setState({ loading: false, imgUrl: null });
                    Notification(res.error, 'error');
                }
            });
        });
        console.log(imgUrl);
    };

    toClose = () => {
        let postMessageData = {
            key: 'close',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

  toSettle = () => {
    let { imgUrl, fileList, imgId } = this.state;
    let values = {
      imgUrl: imgUrl,
      fileList: fileList,
      imgId: imgId,
    };
    console.log(values)
    this.props.saveFormData(values, 'formCate', true, 'settle');//settle走暂存，save走保存
  }

    render () {
        let _this = this;
        let {
            formdata,
            isModalVisible,
            imgUrl,
            fileList,
            previewVisible,
            previewImage,
            loading,
        } = this.state;
        console.log(formdata);
        const disabledConfig = formdata.disabled ? formdata.disabled : false;
        const props = {
            name: 'files',
            // https://hkbudtest.com:9992
            action: `${process.env.REACT_APP_API}${URL.ATTACHMENT}/upload?sourceType=${'AML_PROFILE'}`,
            headers: {
                authorization: 'authorization-text',
            },
            accept: 'image/jpeg,image/png',
            beforeUpload: async (file) => {
                /**
                 * 针对图片进行压缩,如果图片大小超过压缩阈值,则执行压缩,否则不压缩
                 */
                let fileSize = file.size / 1024 / 1024;
                if ((fileSize >= 1.5)) {
                    // 判断文件是不是jpeg 不是jpeg的都转成jpeg 
                    if (!['jpeg', 'jpg'].includes(file.type)) {
                        file = await ConvertImage(file);  //转陈jpeg格式的file
                    }
                    let newFile = await ImageCompressor(file, 'file', 0.6); //图片压缩
                    return newFile;
                } else {
                    return file;
                }
            },
            onChange (info) {
                console.log(info);
                if (info.file.status !== 'uploading') {
                }
                if (info.file.status === 'done') {
                    console.log(info.file, info.fileList);
                    if (info.file.response && info.file.response.status === 200) {
                        // _this.setState({
                        //   fileList: [..._this.state.fileList, info.file.response.data[0]],
                        // });
                        Notification(`${info.file.name} file uploaded successfully`, 'success');
                    } else {
                        Notification(`${info.file.name}: ${info.file.response.message}`, 'error');
                    }
                } else if (info.file.status === 'error') {
                    Notification(`${info.file.name} file upload failed.`, 'error');
                }
                let newFileList = info.fileList.map((ele) => {
                    if (ele.response && ele.response.status !== 200) {
                        ele.status = 'error';
                        return ele;
                    } else {
                        return ele;
                    }
                });
                let newarr = [];
                newFileList.map((ele) => {
                    if (ele.status === 'error') {
                    } else {
                        newarr.push(ele);
                    }
                    return false
                });
                _this.setState({
                    fileList: newarr,
                });
            },
        };
        console.log(fileList);
        return (
            <div className="antiMoneyFormCertificate">
                <Spin spinning={loading}>
                    <Row>
                        <Col span={24}>
                            {!disabledConfig && (
                                <div className="title">Upload IC and signature</div>
                            )}
                        </Col>
                        <Col>
                            {!disabledConfig && (
                                <>
                                    <span>Upload IC Images： </span>{' '}
                                    <Upload
                        key={'Upload'}
                                        {...props}
                                        capture={'camera'}
                                        multiple={true}
                                        listType="picture"
                                        disabled={disabledConfig}
                                        fileList={[...fileList]}
                                        onPreview={this.handlePreview}
                                        className="upload-list-inline"
                                    >
                                        <Button
                                            shape="circle"
                                            disabled={disabledConfig}
                                            icon={<UploadOutlined />}
                                        ></Button>
                                    </Upload>
                                </>
                            )}
                        </Col>
                        <Divider />
                        <Col span={24} style={{ height: 270 }}>
                            <p>
                                Sales Staff Signature：{' '}
                                <Button
                                    disabled={disabledConfig}
                                    shape="circle"
                                    icon={<EditOutlined />}
                                    onClick={this.openSignModal}
                                />
                            </p>
                            <br />
                            {imgUrl && !disabledConfig ? (
                                <Image width={400} src={imgUrl} />
                            ) : (
                                ''
                            )}
                            {disabledConfig && (
                                <>
                                    <img
                                        alt=""
                                        className="signImg"
                                        style={{ width: 400 }}
                                        src={`data:image/jpeg;base64,${this.props.formdata.formCate.signBase64}`}
                                    />
                                </>
                            )}

                            <Modal
                                title="Sign"
                                width={700}
                                visible={isModalVisible}
                                onOk={this.handleOk}
                                onCancel={() => this.setState({ isModalVisible: false })}
                            >
                                <AntiMoneyFormSign
                                    onRef={(ref) => {
                                        this.Child = ref;
                                    }}
                                    getImgUrl={this.getImgUrl}
                                />
                            </Modal>
                        </Col>
                    </Row>
                    <Modal
                        visible={previewVisible}
                        footer={null}
                        onCancel={this.handleCancel}
                        width={700}
                    >
                        <br />
                        <img
                            alt="example"
                            style={{ width: '100%' }}
                            src={previewImage}
                        />
                    </Modal>
                    <div className="btn">
                        {this.props.printnow ? (
                            ''
                        ) : (
                            <Space>
                                {formdata.disabled === undefined && (
                                    <Button onClick={this.toClose}>Close</Button>
                                )}
                                <Button onClick={this.goback}>Back</Button>
                      {!formdata.disabled && this.props?.typename !== 'record' ? (
                        <Button disabled={imgUrl && fileList?.length > 0 ? true : false} onClick={this.toSettle}>Settle</Button>
                      ) : (
                        ''
                      )}
                      {!formdata.disabled ? (
                        <Button onClick={this.tosave}>Save</Button>
                      ) : (
                        ''
                      )}
                            </Space>
                        )}
                    </div>
                </Spin>
            </div>
        );
    }
}

export default AntiMoneyFormCertificate;
