import React from 'react';
import { Button } from 'antd';
import './index.less';
import SignatureCanvas from 'react-signature-canvas';
import { DeleteOutlined } from '@ant-design/icons';

class AntiMoneyFormSign extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: null,
    };
  }

  canvas = {};

  //清除画布canvas
  reset = () => {
    this.canvas.clear();
    this.setState({ imgUrl: null });
  };
  //生成图片
  trim = () => {
    this.setState(
      {
        imgUrl: this.canvas.getTrimmedCanvas().toDataURL('image/png'),
      },
      () => {
        this.props.getImgUrl(this.state.imgUrl);
      }
    );
  };

  componentWillReceiveProps(nextProps) {}

  componentDidMount() {
    this.props.onRef(this);
  }

  render() {
    // let { imgUrl } = this.state;
    return (
      <div className="antiMoneyFormSign">
        <div className="cleanBtn">
          <Button
            shape="circle"
            icon={<DeleteOutlined />}
            onClick={this.reset}
          />
        </div>
        <SignatureCanvas
          penColor="#505766"
          canvasProps={{ className: 'sigCanvas' }}
          ref={(ref) => {
            this.canvas = ref;
          }}
          backgroundColor="#fff"
        ></SignatureCanvas>
      </div>
    );
  }
}

export default AntiMoneyFormSign;
