import React from 'react';
import './index.less';
import AntiMoneyFormA from './formA';
import AntiMoneyFormB from './formB';
import AntiMoneyFormBod from './formBod';
import JsPDF from 'jspdf';
import html2Canvas from 'html2canvas';
import AntiMoneyFormE from './formE';
import AntiMoneyFormCertificate from './formCertificate';
import { getCountry, getOccupation, getCheckRef, saveAnti } from '@/api/antiM';
import moment from 'moment';
import { Spin, Button } from 'antd';
import { PrinterOutlined } from '@ant-design/icons';
import Notification from '@/components/Notification/index'
import { formatDate_hh_mm_ss, formatDate } from '@/utils/formatDate';
import { withRouter } from "react-router-dom";
import { getUrlParams } from "@/utils/getUrl";

const format = 'YYYY-MM-DD HH:mm:ss';
const format2 = 'YYYY-MM-DD';
class AntiMoney extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showType: 'formA',
      // showType: 'formCate',
      postMessagedata: {},
      refArr: {},
      formdata: {},
      checkref: {},
      loading: false,
      formSMS: {},
      printnow: false,
      editPostMessagedata:{}
    };
  }

  componentWillMount() {
    this.getref();
    this.getCheckRef(); //记得打开

    if (this.props.postMessagedata) {
      if (this.props.postMessagedata?.cddForm?.cddDate) {
        this.props.postMessagedata.cddForm.cddDate = moment(this.props.postMessagedata?.cddForm?.cddDate).format(formatDate)
      }
      let obj = {
        formA: { ...this.props.postMessagedata.cddForm },
        formB: { ...this.props.postMessagedata.cddForm },
        formE: this.props.postMessagedata.ecddForm
          ? {
              ...this.props.postMessagedata.ecddForm,
              ...this.props.postMessagedata.cddForm,
            }
          : false,
        formBod: this.props.postMessagedata.bodForm
          ? {
              ...this.props.postMessagedata.bodForm,
            }
          : false,
        formCate: this.props.postMessagedata.sign
          ? {
              ...this.props.postMessagedata.sign,
              imgUrl: this.props.postMessagedata.sign.url,
              signBase64: this?.props?.postMessagedata?.signBase64 || false,
            }
          : false,
        disabled: this.props.disabled,
      };
      if (this.props.disabled === false) {
        obj.formE = {
          ...obj.formE,
          ...this.props.postMessagedata.cddForm
        }
      }
      console.log(obj);
      this.setState({
        formdata: obj,
        editPostMessagedata:this.props.postMessagedata
      });
    }
    if (this.props.location && this.props.location.query) {
      this.setState({
        postMessagedata: {
          ...this.props.location.query.postMessagedata,
          formSMS: this.props.location.query.formSMS,
        },
        formSMS: this.props.location.query.formSMS,
      });
    }
  }

  getCheckRef = () => {
    let { checkref } = this.state;
    this.setState({ loading: true });
    let params = [
      'id_type',
      'annual_income',
      'wealth_source',
      'funds_source',
      'info_source',
    ];
    params.map((ele) => {
      getCheckRef(ele).then((res) => {
        console.log(res);
        if (res.status === 200) {
          checkref[ele] = res.data;
          this.setState({ checkref }, () => {
            if (Object.keys(this.state.checkref).length === 7) {
              this.setState({ loading: false });
            }
          });
        } else {
          // Notification(res?.message||'error', 'error');
          checkref[ele] = [];
          if (Object.keys(checkref).length === 7) {
            this.setState({ loading: false });
          }
          this.setState({ checkref });
        }
      });
      return false;
    });
  };

  componentDidMount() {
    // let postMessageData = {
    //   key: 'ok',
    // };
    // window.parent.postMessage(JSON.stringify(postMessageData), '*');
  }

  getref = () => {
    let { refArr, checkref } = this.state;
    this.setState({ loading: true });
    getCountry().then((res) => {
      if (res && res.status === 200) {
        let obj = {
          selectData: res.data.recordList,
          total: res.data.recordList.length,
        };
        refArr.Nationality = obj;
        checkref['getCountry'] = refArr;
        this.setState({ refArr: refArr, checkref }, () => {
          if (Object.keys(this.state.checkref).length === 7) {
            this.setState({ loading: false });
          }
          console.log(refArr);
        });
      } else {
        // Notification(res.message, 'error');
        checkref['errgetCountry'] = false;
        if (Object.keys(checkref).length === 7) {
          this.setState({ loading: false });
        }
        this.setState({ checkref });
      }
    });
    getOccupation().then((res) => {
      if (res && res.status === 200) {
        let obj = {
          selectData: res.data.recordList,
          total: res.data.recordList.length,
        };
        refArr.Occupation = obj;
        checkref['getOccupation'] = refArr;
        this.setState({ refArr: refArr, checkref }, () => {
          if (Object.keys(this.state.checkref).length === 7) {
            this.setState({ loading: false });
          }
          console.log(refArr);
        });
      } else {
        // Notification(res?.message||'error', 'error');
        checkref['errOccupation'] = false;
        if (Object.keys(checkref).length === 7) {
          this.setState({ loading: false });
        }
        this.setState({ checkref });
        // this.setState({ loading: false });
      }
    }).catch((err) => {
      checkref['errOccupation'] = false;
      if (Object.keys(checkref).length === 7) {
        this.setState({ loading: false });
      }
      this.setState({ checkref });
    });
  };

  refFormA = (ref) => {
    this.refFormA = ref;
  };

  refFormB = (ref) => {
    this.refFormB = ref;
  };

  changeShowtype = (type) => {
    this.setState({ showType: type });
  };

  saveFormData = (data, type, flag, savetype) => {
    let { formdata, formSMS, postMessagedata, editPostMessagedata } =
      this.state;
    formdata[type] = data;
    if (type === 'formB' && flag) {
      formdata['formE'] = false;
    }
    this.setState({ formdata }, () => {
      console.log(this.state.formdata);
      if (flag) {
        let param = {
          cddFormDto: {
            ...formdata.formA,
            ...formdata.formB,
            ...formSMS,
            finishFill: savetype === 'save' ? true : false,
            cashOver20k: postMessagedata?.checkData?.cashOver20k || this?.props?.postMessagedata?.cddForm?.cashOver20k
          },
        };
        console.log(param)
        if (this.props.disabled === false) {
          console.log(editPostMessagedata);
          console.log(formdata);
          param.cddFormDto.id = editPostMessagedata?.cddForm?.id || '';
        } else {
          param.profileReport = postMessagedata?.checkData?.profileReport
            ? postMessagedata.checkData.profileReport
            : '';
        }
        if (formdata.formCate) {
          let data = [formdata.formCate.imgId];
          formdata.formCate.fileList.map((ele) => {
            if (ele.response.status === 200) {
              data.push(ele.response.data[0].id);
            }
            return false;
          });
          param.attachmentIds = data;
        }
        if (formdata.formBod) {
          param.bodFormDto = {
            ...formdata.formBod,
          };
          param.bodFormDto.mBirth = moment(param.bodFormDto.mBirth).format(
            format
          );
          if (param.bodFormDto.mIdExpiryDate) {
            param.bodFormDto.mIdExpiryDate = moment(
              param.bodFormDto.mIdExpiryDate
            ).format(format);
          }
        }
        if (formdata.formE) {
          param.ecddFormDto = {
            ...formdata.formE,
          };
          param.ecddFormDto.cddDate = moment(
            param.ecddFormDto.cddDate,
            formatDate_hh_mm_ss
          ).format(format2);
        }
        param.cddFormDto.cddDate = moment(param.cddFormDto.cddDate).format(
          format2
        );
        param.cddFormDto.txnTime = moment(param.cddFormDto.txnTime).format(
          format
        );
        param.cddFormDto.mBirth = moment(param.cddFormDto.mBirth).format(
          format
        );
        if (param.cddFormDto.mIdExpiryDate) {
          param.cddFormDto.mIdExpiryDate = moment(
            param.cddFormDto.mIdExpiryDate
          ).format(format);
        }
        console.log(param);
        this.setState({ loading: true });
        saveAnti(param).then((res) => {
          console.log(res);
          this.setState({ loading: false });
          if (res.status === 200) {
            if (this.props.disabled === false) {
              this.props.closeModal();
            } else {
              let postMessageData = {
                key: 'finish',
                id: res.data.id,
              };
              window.parent.postMessage(JSON.stringify(postMessageData), '*');
            }
          } else {
            if (res.message) Notification(res.message, 'error');
          }
        });
      }
    });
  };

  tosaveInfo = () => {
    let { formdata } = this.state;
    console.log(formdata);
  };


  addDom = () => {
    let { formdata } = this.state
    console.log(formdata)
    // 'formBod','formE'
    let classnameArr = ['formA', 'formB']
    if (formdata.formBod) {
      classnameArr.push('formBod')
    }
    if (formdata.formE) {
      classnameArr.push('formE')
    }
    classnameArr.map(ref => {
      let modal = document.getElementById(ref)
      // let domarr12 = modal.getElementsByClassName('ant-col ant-col-12')
      // let domarr16 = modal.getElementsByClassName('ant-col ant-col-16')
      // let domarr24 = modal.getElementsByClassName('ant-col ant-col-24')
      // let domtypography = modal.getElementsByClassName('ant-typography')
      // let domsignImg = modal.getElementsByClassName('signImg')

      let modalH = Math.ceil(modal.clientHeight / 1230)
      let newdom = document.createElement("div")
      newdom.className = 'newdom';
      // newdom.innerHTML = "分页"
      newdom.style.width = `100%`
      // newdom.style.background= `red`
      let height = modalH * 1230 - modal.clientHeight
      newdom.style.height = `${height}px`
      modal.appendChild(newdom)

      // let domarr = [...domarr12,...domarr16,...domarr24,...domtypography,...domsignImg]
      // let addhight = 0
      // domarr.map((ele,index)=>{
      //   if(parseInt((ele.offsetTop+addhight)/1230) !== parseInt(((ele.offsetTop+addhight)+ele.clientHeight)/1230)){
      //     console.log('不在一页')
      //     console.log(ele.offsetTop)
      //     console.log(ele.clientHeight)
      //     console.log((ele.offsetTop)+ele.clientHeight)
      //     console.log(parseInt((ele.offsetTop+100)/1230))
      //     console.log(parseInt(((ele.offsetTop+100)+ele.clientHeight)/1230))
      //     let newdom = document.createElement("div")
      //     newdom.className = 'newdom';
      //     newdom.innerHTML = "分页"
      //     let height = (Math.ceil((ele.offsetTop+addhight)/1230)-(ele.offsetTop+addhight)/1230) *1230
      //     console.log(height)
      //     newdom.style.height= `${height}px`
      //     addhight += height
      //     console.log(addhight)
      //     newdom.style.width= `100%`
      //     newdom.style.background= `red`
      //     // ele.parentNode.insertBefore(newdom, ele)
      //   }
      // })

    })

  }


  toprint = () => {
    let _this = this;
    this.setState({ printnow: true }, () => {
      this.addDom()
      html2Canvas(document.getElementById('printArea'), {
        backgroundColor: '#ffffff',
        allowTaint: true, //开启跨域
        useCORS: true,
        scrollY: 0,
        scrollX: 0,
        y: -20,
        // x: 30,
      }).then((canvas) => {
        // console.log('canvas', canvas);
        let contentWidth = canvas.width;
        let contentHeight = canvas.height;
        // 一页pdf显示html页面生成的canvas高度;
        let pageHeight = (contentWidth / 592.28) * 841.89;
        // 未生成pdf的html页面高度
        let leftHeight = contentHeight;
        // 页面偏移
        let position = 20;
        // a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高
        let imgWidth = 555.28;
        let imgHeight = (595.28 / contentWidth) * contentHeight;
        let pageData = canvas.toDataURL('image/jpeg', 1.0);
        let pdf = new JsPDF(null, 'pt', 'a4');
        // 有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
        // 当内容未超过pdf一页显示的范围，无需分页
        if (leftHeight < pageHeight) {
          pdf.addImage(pageData, 'JPEG', 20, 20, imgWidth, imgHeight);
        } else {
          while (leftHeight > 0) {
            pdf.addImage(pageData, 'JPEG', 20, position, imgWidth, imgHeight);
            leftHeight -= pageHeight;
            // 避免添加空白页
            position -= 841.89;
            if (leftHeight > 0) {
              pdf.addPage();
            }
          }
        }
        pdf.autoPrint();
        pdf.output('dataurlnewwindow');
      });
      _this.setState({ printnow: false });
    });
  };

  render() {
    let {
      showType,
      postMessagedata,
      refArr,
      formdata,
      checkref,
      loading,
      printnow,
      editPostMessagedata
    } = this.state;
    console.log(postMessagedata);
    let {system} = this.props.location.search && getUrlParams(this.props.location.search)
    return (
      <div className="antiMoney">
        {system?.toUpperCase() === "BIP"  && formdata && formdata.disabled && showType === 'formA' ? (
          <span className="printSty">
            <Button icon={<PrinterOutlined />} onClick={this.toprint}></Button>
          </span>
        ) : (
          ''
        )}
        <span key={printnow} id="printArea">
          <Spin spinning={loading}>
            {showType === 'formA' ? (
              <AntiMoneyFormA
                key={showType}
                changeShowtype={this.changeShowtype}
                postMessagedata={postMessagedata}
                refArr={refArr}
                saveFormData={this.saveFormData}
                formdata={formdata}
                checkref={checkref}
                printnow={printnow}
              />
            ) : (
              ''
            )}
            {showType === 'formBod' || (printnow && formdata.formBod) ? (
              <AntiMoneyFormBod
                postMessagedata={postMessagedata}
                key={showType}
                refArr={refArr}
                changeShowtype={this.changeShowtype}
                saveFormData={this.saveFormData}
                formdata={formdata}
                checkref={checkref}
                printnow={printnow}
              />
            ) : (
              ''
            )}
            {showType === 'formB' || (printnow && formdata.formB) ? (
              <AntiMoneyFormB
                key={showType}
                changeShowtype={this.changeShowtype}
                postMessagedata={postMessagedata}
                saveFormData={this.saveFormData}
                formdata={formdata}
                tosaveInfo={this.tosaveInfo}
                printnow={printnow}
              />
            ) : (
              ''
            )}
            {showType === 'formE' || (printnow && formdata.formE) ? (
              <AntiMoneyFormE
                postMessagedata={postMessagedata}
                key={showType}
                changeShowtype={this.changeShowtype}
                saveFormData={this.saveFormData}
                formdata={formdata}
                checkref={checkref}
                tosaveInfo={this.tosaveInfo}
                printnow={printnow}
              />
            ) : (
              ''
            )}
            {showType === 'formCate' || (printnow && formdata.formCate) ? (
              <AntiMoneyFormCertificate
                postMessagedata={postMessagedata}
                key={showType}
                changeShowtype={this.changeShowtype}
                saveFormData={this.saveFormData}
                formdata={formdata}
                checkref={checkref}
                tosaveInfo={this.tosaveInfo}
                printnow={printnow}
                editPostMessagedata={editPostMessagedata}
                typename={this?.props?.typename || ''}
              />
            ) : (
              ''
            )}
          </Spin>
        </span>
      </div>
    );
  }
}

export default withRouter(AntiMoney);
