import React from 'react';
import {
  DatePicker,
  Form,
  Input,
  Button,
  Row,
  Col,
  Divider,
  Radio,
  Space,
} from 'antd';
import './index.less';
import SelectCommon from '@/components/selectCommon/index';
import moment from 'moment';

const { TextArea } = Input;

class AntiMoneyFormBod extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      formRef: '',
      refArr: this.props.refArr,
      formdata: this.props.formdata,
      checkref: this.props.checkref,
      mIdType: true,
    };
  }

  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(nextProps.refArr) !== this.props.refArr) {
      this.setState({ refArr: nextProps.refArr }, () => {});
    }
    if (JSON.stringify(nextProps.formdata) !== this.props.formdata) {
      this.setState({ formdata: nextProps.formdata }, () => {
        if (
          nextProps.formdata.formBod &&
          nextProps.formdata.formBod.mIdType === 'NRIC'
        ) {
          this.setState({ mIdType: false });
        }
        this.formRef.current.resetFields();
      });
    }
    if (JSON.stringify(nextProps.checkref) !== this.props.checkref) {
      this.setState({ checkref: nextProps.checkref }, () => {});
    }
  }


  toClose = () => {
    let postMessageData = {
      key: 'close',
    };
    window.parent.postMessage(JSON.stringify(postMessageData), '*');
  };

  changemIdType = (e) => {
    console.log(e);
    this.setState({ mIdType: e.target.value === 'NRIC' ? false : true });
  };

  componentDidMount() {
    console.log(this.props);
    this.setState({ formRef: this.formRef });
    this.formRef.current.resetFields();
    if (
      this.props.formdata.formBod &&
      this.props.formdata.formBod.mIdType === 'NRIC'
    ) {
      this.setState({ mIdType: false });
    }
    // this.props.formB(this);
  }

  goback = () => {
    this.formRef.current
      .validateFields()
      .then((values) => {
        console.log(values);
        this.props.saveFormData(values, 'formBod');
        this.props.changeShowtype('formA');
      })
      .catch((errorInfo) => {
        this.props.saveFormData(errorInfo.values, 'formBod');
        this.props.changeShowtype('formA');
        console.log(errorInfo);
      });
  };

  jumpNext = () => {
    this.formRef.current
      .validateFields()
      .then((values) => {
        console.log(values);
        this.props.saveFormData(values, 'formBod');
        this.props.changeShowtype('formB');
      })
      .catch((errorInfo) => {});
  };

  formRef = React.createRef();

  render() {
    let { formRef, refArr, formdata, checkref, mIdType } = this.state;
    const layout = {
      labelCol: {
        span: 10,
      },
      wrapperCol: {
        span: 14,
      },
    };
    const layout2 = {
      labelCol: {
        span: 5,
      },
      wrapperCol: {
        span: 19,
      },
    };
    const ruleConfig = {
      rules: [{ required: true, message: 'Please enter required fields' }],
    };
    // const ruleConfigselect = {
    //   rules: [{ required: true, message: 'Please select required fields' }],
    // };
    const disabledConfig = {
      disabled: formdata.disabled ? formdata.disabled : false,
    };
    let initialValuesConfig =
      formdata && formdata.formBod
        ? {
            ...formdata.formBod,
            newmBirth: formdata
              ? formdata.formBod.mBirth
                ? moment(formdata.formBod.mBirth).format('MM/DD/YYYY')
                : ''
              : '',
            newmIdExpiryDate: formdata
              ? formdata.formBod.mIdExpiryDate
                ? moment(formdata.formBod.mIdExpiryDate).format('MM/DD/YYYY')
                : ''
              : '',
            mNationalityId:
              formdata && formdata.formBod && formdata.formBod.mNationalityId
                ? formdata.formBod.mNationalityId.toString()
                : '',
            mOccupationId:
              formdata && formdata.formBod && formdata.formBod.mOccupationId
                ? formdata.formBod.mOccupationId.toString()
                : '',
          }
        : {};
    return (
      <div className="antiMoneyformBod">
        <Form
          {...layout}
          name="formBod"
          initialValues={{
            ...initialValuesConfig,
          }}
          ref={this.formRef}
          colon={false}
          requiredMark={false}
          // onFinish={onFinish}
          // onFinishFailed={onFinishFailed}
        >
          <Row>
            <Col span={24}>
              <div className="title">Beneficial Owner Details</div>
            </Col>
            <Col span={24}>
              <Form.Item
                {...ruleConfig}
                label="Full Name (including any alias)"
                name="mName"
                {...layout2}
              >
                <Input {...disabledConfig} />
              </Form.Item>
            </Col>
            <Col span={12}>
              {this.props.printnow ? (
                <Form.Item
                  {...ruleConfig}
                  label="Date Of Birth"
                  name="newmBirth"
                >
                  <Input disabled />
                </Form.Item>
              ) : (
                <Form.Item {...ruleConfig} label="Date Of Birth" name="mBirth">
                  <DatePicker format={'MM/DD/YYYY'} {...disabledConfig} />
                </Form.Item>
              )}
            </Col>
            <Col span={24}>
              <Form.Item
                {...ruleConfig}
                label="Residential Address"
                name="mAddress"
                {...layout2}
              >
                <TextArea {...disabledConfig} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...ruleConfig} label="Contact Number" name="mPhone">
                <Input {...disabledConfig} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...ruleConfig}
                label="Nationality"
                name={
                  this.props.printnow ? 'mNationalityName' : 'mNationalityId'
                }
              >
                {this.props.printnow ? (
                  <Input disabled />
                ) : (
                  <SelectCommon
                    {...disabledConfig}
                    type="mNationalityId"
                    refArr={refArr.Nationality ? refArr.Nationality : []}
                    formRef={formRef.current}
                  />
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...ruleConfig}
                label="Type of ID Document"
                name="mIdType"
              >
                <Radio.Group {...disabledConfig} onChange={this.changemIdType}>
                  {checkref.id_type
                    ? checkref.id_type.map((ele) => {
                        return <Radio value={ele.code}>{ele.name}</Radio>;
                      })
                    : ''}
                </Radio.Group>
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item {...ruleConfig} label="ID Number" name="mIdNumber">
                <Input {...disabledConfig} />
              </Form.Item>
            </Col>
            <Col span={12}>
              {(mIdType && !this.props.printnow) ||
              (formdata.disabled && initialValuesConfig?.mIdType !== 'NRIC') ? (
                <Form.Item
                  rules={[
                    {
                      validator: (_, value) => {
                        console.log(value);
                        if (!value) {
                          return Promise.reject(
                            new Error('Please enter required fields')
                          );
                        } else {
                          // if (moment(value).diff(moment(), 'month') < 6) {
                          //   return Promise.reject(
                          //     new Error(
                          //       'The date of ID expiry cannot be less than 6 months from the current date'
                          //     )
                          //   );
                          // }
                          return Promise.resolve();
                        }
                      },
                    },
                  ]}
                  label="Date Of ID Expiry"
                  name={
                    this.props.printnow ? 'newmIdExpiryDate' : 'mIdExpiryDate'
                  }
                >
                  {this.props.printnow ? (
                    <Input disabled />
                  ) : (
                    <DatePicker format={'MM/DD/YYYY'} {...disabledConfig} />
                  )}
                </Form.Item>
              ) : (
                ''
              )}
            </Col>
            <Col span={24}>
              <Form.Item
                {...ruleConfig}
                label="Occupation"
                name={this.props.printnow ? 'mOccupationName' : 'mOccupationId'}
                {...layout2}
              >
                {this.props.printnow ? (
                  <Input disabled />
                ) : (
                  <SelectCommon
                    type="mOccupationId"
                    refArr={refArr.Occupation ? refArr.Occupation : []}
                    formRef={formRef.current}
                    {...disabledConfig}
                  />
                )}
              </Form.Item>
            </Col>
            <Divider />
          </Row>
        </Form>
        <div className="btn">
          {this.props.printnow ? (
            ''
          ) : (
            <Space>
              {formdata.disabled === undefined && (
                <Button onClick={this.toClose}>Close</Button>
              )}
              <Button onClick={this.goback}>Back</Button>
              <Button onClick={this.jumpNext}>Next</Button>
            </Space>
          )}
        </div>
      </div>
    );
  }
}

export default AntiMoneyFormBod;
