//获取url中的参数
export function getUrlParams(url) {
    let jsonList = {};
    if (url.indexOf("?") > -1) {
        let str = url.slice(url.indexOf("?") + 1);
        let strs = str.split("&");
        for (let i = 0; i < strs.length; i++) {
            jsonList[strs[i].split("=")[0]] = strs[i].split("=")[1];//如果出现乱码的话，可以用decodeURI()进行解码
        }
    }
    return jsonList
}