import React from 'react';
import { Select, Divider, Pagination, Spin, Empty } from 'antd';
// import { getCountry, getOccupation } from '@/api/antiM';

const { Option } = Select;
let timeid = null;
class SelectCommon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            current: 1,
            total: 0,
            pageSize: 10,
            selectData: [],
            loading: false,
            searchloading: false,
            type: this.props.type,
            value: this.props.value,
        };
    }

    componentWillReceiveProps(nextProps) {
        // if (
        //   JSON.stringify(nextProps.postMessagedata) !== this.props.postMessagedata
        // ) {
        //   this.setState({ postMessagedata: nextProps.postMessagedata }, () => {
        //     this.formRef.current.resetFields();
        //   });
        // }
    }

    componentDidMount() {
        // this.props.formRef.setFieldsValue({
        //   [this.props.type]:this.props.value
        // })
        this.setState(
            {
                selectData: this.props.refArr.selectData,
                total: this.props.refArr.total,
            },
            () => {
                if (this.state.selectData && this.props.value)
                    this.state.selectData.map((ele, index) => {
                        if (ele.id === this.props.value) {
                            this.setState({ current: Math.ceil((index + 1) / 10) });
                        }
                        return false
                    });
            }
        );
    }

    // getRef = (page) => {
    //   let { current, pageSize, type } = this.state;
    //   console.log(type);
    //   let params = {
    //     // pageIndex: page ? page : current,
    //     // pageSize: pageSize,
    //   };
    //   this.setState({ loading: true });
    //   if (type === 'Nationality') {
    //     getCountry(params).then((res) => {
    //       if (res.status === 200) {
    //         this.setState({
    //           selectData: res.data.recordList,
    //           total: res.data.recordList.length,
    //           // current: res.data.pageIndex,
    //           loading: false,
    //         });
    //       } else {
    //         this.setState({ loading: false });
    //       }
    //     });
    //   } else if (type === 'Occupation') {
    //     getOccupation(params).then((res) => {
    //       if (res.status === 200) {
    //         this.setState({
    //           selectData: res.data.recordList,
    //           total: res.data.recordList.length,
    //           // current: res.data.pageIndex,
    //           loading: false,
    //         });
    //       } else {
    //         this.setState({ loading: false });
    //       }
    //     });
    //   }
    // };

    onChange = (page) => {
        // console.log(page);
        // this.getRef(page);
        this.setState({ current: page });
    };

    changeValue = (value) => {
        this.setState({ value: value }, () => {
            this.props.formRef.setFieldsValue({
                [this.props.type]: value,
            });
            this.setState(
                {
                    selectData: this.props.refArr.selectData,
                    total: this.props.refArr.total,
                },
                () => {
                    if (this.state.selectData && value)
                        this.state.selectData.map((ele, index) => {
                            if (ele.id === this.props.value) {
                                this.setState({ current: Math.ceil((index + 1) / 10) });
                            }
                            return false
                        });
                }
            );
        });
    };

    onSearch = (val) => {
        console.log('search:', val);
        if (val !== '') {
            if (timeid) {
                clearTimeout(timeid);
            }
            if (
                this.props.refArr.selectData &&
                this.props.refArr.selectData.length > 0
            ) {
                this.setState(
                    { selectData: [], total: 0, searchloading: true },
                    () => {
                        timeid = setTimeout(() => {
                            let newarr = [];
                            this.props.refArr.selectData.map((ele, index) => {
                                if (ele.name.toLowerCase().indexOf(val.toLowerCase()) >= 0) {
                                    newarr.push(ele);
                                }
                                return false
                            });
                            this.setState(
                                {
                                    selectData: newarr,
                                    total: newarr.length,
                                    searchloading: false,
                                    current: 1,
                                },
                                () => {
                                    clearTimeout(timeid)
                                }
                            );
                        }, 2000);
                    }
                );
                // }
            }
        } else {
            this.setState({
                selectData: this.props.refArr.selectData,
                total: this.props.refArr.total,
            });
        }
    };

    render() {
        const {
            current,
            total,
            pageSize,
            selectData,
            loading,
            value,
            searchloading,
        } = this.state;
        return (
            <Select
                key={this.props.type}
                showSearch={true}
                onSearch={this.onSearch}
                loading={loading}
                value={value}
                onChange={this.changeValue}
                disabled={this.props.disabled ? this.props.disabled : false}
                notFoundContent={
                    searchloading ? (
                        <Spin size="small" spinning={searchloading} />
                    ) : (
                        <Empty />
                    )
                }
                filterOption={(input, option) => {
                    // console.log('1111');
                    // console.log(
                    //   option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    // );
                    return (
                        true
                    );
                }}
                // onDropdownVisibleChange={(open) => {
                //   if (open && selectData.length === 0) {
                //     this.getRef();
                //   }
                // }}
                dropdownRender={(menu) => (
                    <div>
                        {menu}
                        <Divider style={{ margin: '4px 0' }} />
                        <div
                            style={{
                                display: 'flex',
                                flexWrap: 'nowrap',
                                padding: 8,
                            }}
                        >
                            <Pagination
                                current={current}
                                onChange={this.onChange}
                                total={total}
                                pageSize={pageSize}
                                showSizeChanger={false}
                            />
                        </div>
                    </div>
                )}
            >
                {selectData &&
                    selectData.slice(current * 10 - 10, current * 10).map((ele) => {
                        return <Option key={ele.id} value={ele.id}>{ele.name}</Option>;
                    })}
            </Select>
        );
    }
}

export default SelectCommon;
