import React from 'react';
import {
    Form,
    Input,
    Button,
    Row,
    Col,
    Divider,
    Space,
    Spin,
    Table,
    Modal,
    Upload,
    Image,
} from 'antd';
import './index.less';
import { getamlByid, getamlform, getAtta, saveAtta } from '@/api/antiM';
import moment from 'moment';
import { getUrlParams } from '@/utils/getUrl';
import { UploadOutlined } from '@ant-design/icons';
import AntiMoney from '../antiMoney';
import Notification from '@/components/Notification/index'
import URL from '@/api/URL'


import ConvertImage from '@/utils/ConvertImage.js';
import ImageCompressor from '@/utils/ImageCompressor.js';

const format2 = 'MM/DD/YYYY HH:mm:ss';
const { TextArea } = Input;
class AmlDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            baseInfo: {},
            current: 1,
            total: 0,
            pageSize: 10,
            isModalVisible: false,
            previewVisible: false,
            visibleImg: false,
            previewImg: '',
            postMessagedata: {},
            PDFdata: [],
            CTRdata: [],
            previewImage: ''
        };
    }

    componentWillReceiveProps (nextProps) { }

    componentDidMount () {
        let url = window.location.href;
        let params = getUrlParams(url);
        console.log(params);
        this.getbacsInfo(params); //查禁用信息
    }

    tosaveAtta = () => {
        let { baseInfo, CTRdata } = this.state;
        let attachArr = [];
        CTRdata.map((ele) => {
            attachArr.push(ele.id);
            return false
        });
        let param = {
            outId: baseInfo.cddFormId,
            sourceType: 'AML_CTR',
            attachmentIds: attachArr,
        };
        this.setState({ loading: true });
        saveAtta(param).then((res) => {
            if (res.status === 200) {
                Notification(res.message, 'success');
                this.setState({ loading: false }, () => {
                    this.props.history.goBack();
                });
            } else {
                Notification(res.message, 'error');
                this.setState({ loading: false });
            }
        });
    };

    getPDF_CTR = (outId) => {
        let param = {
            outId: outId,
            sourceType: 'AML_PROFILE',
        };
        getAtta(param).then((res) => {
            console.log(res);
            if (res.status === 200) {
                this.setState({ PDFdata: res.data });
            } else {
                Notification(res.message, 'error');
            }
        });

        let param2 = {
            outId: outId,
            sourceType: 'AML_CTR',
        };
        getAtta(param2).then((res) => {
            console.log(res);
            if (res.status === 200) {
                this.setState({ CTRdata: res.data });
            } else {
                Notification(res.message, 'error');
            }
        });
    };

    getbacsInfo = (params) => {
        this.setState({ loading: true });
        getamlByid(params.id).then((res) => {
            console.log(res);
            if (res instanceof Object) {
                this.setState({ baseInfo: res.data, loading: false, previewImage: res.data.sign ? res.data.sign.url : '' }, () => {
                    this.getPDF_CTR(res.data.cddFormId);
                    this.formRef.current.resetFields();
                });
            } 
        });
    };

    onChange = () => { };

    openModal = (cddFormId) => {
        this.setState({ loading: true });
        getamlform(cddFormId).then((res) => {
            this.setState({ loading: false });
            console.log(res);
            if (res instanceof Object) {
                res.data.cddForm.mBirth = res.data.cddForm.mBirth
                    ? moment(res.data.cddForm.mBirth)
                    : '';
                res.data.cddForm.mIdExpiryDate = res.data.cddForm.mIdExpiryDate
                    ? moment(res.data.cddForm.mIdExpiryDate)
                    : '';
                if (res.data.bodForm) {
                    res.data.bodForm.mBirth = res.data.bodForm.mBirth
                        ? moment(res.data.bodForm.mBirth)
                        : '';
                    res.data.bodForm.mIdExpiryDate = res.data.bodForm.mIdExpiryDate
                        ? moment(res.data.bodForm.mIdExpiryDate)
                        : '';
                }
                this.setState({ postMessagedata: res.data }, () => {
                    this.setState({ isModalVisible: true });
                });
            }
        });
    };

    tosearch = () => {
        this.formRef.current
            .validateFields()
            .then((values) => {
                console.log(values);
            })
            .catch((errorInfo) => { });
    };

    toClose = () => {
        let { baseInfo } = this.state
        this.setState({ isModalVisible: false }, () => {
            this.getPDF_CTR(baseInfo.cddFormId);
        });
    };

    formRef = React.createRef();

    toback = () => {
        this.props.history.goBack();
    };

    openSign = () => {
        this.setState({ previewVisible: true });
    }

    handleCancel = () => {
        this.setState({ isModalVisible: false, previewVisible: false });
    };

    todown = (url) => {
        window.open(url);
    };

    getcolumns = (type) => {
        let columns = [
            {
                title: 'Attachment',
                dataIndex: 'originalFileName',
                key: 'originalFileName',
                render: (text, record) => (
                    <button
                        type='button'
                        className='btn-link-blue link-button'
                        onClick={() => {
                            this.setState({ visibleImg: true, previewImg: record.url })
                        }}
                    >{text}</button>
                ),
            },
            {
                title: 'Upload time',
                dataIndex: 'createdTime',
                key: 'createdTime',
            },
        ];
        if (type === 2) {
            columns.push({
                title: '',
                dataIndex: 'id',
                key: 'id',
                render: (text) => (
                    <Button onClick={() => this.toDelet(text)}>Delete</Button>
                ),
            });
        }

        return columns;
    };

    toDelet = (id) => {
        let { CTRdata } = this.state;
        let newCTRdata = [];
        CTRdata.map((ele) => {
            if (ele.id !== id) {
                newCTRdata.push(ele);
            }
            return false
        });
        this.setState({ CTRdata: newCTRdata });
    };

    render () {
        let _this = this;
        let {
            loading,
            baseInfo,
            isModalVisible,
            postMessagedata,
            CTRdata,
            PDFdata,
            previewVisible,
            previewImage,
            visibleImg,
            previewImg
        } = this.state;
        const layout = {
            labelCol: {
                span: 7,
            },
            wrapperCol: {
                span: 14,
            },
        };
        const props = {
            name: 'files',
            action: `${process.env.REACT_APP_API}${URL.ATTACHMENT}/upload?sourceType=AML_CTR`,
            // action: `/gateway/skbiz/api/v1/attachment/upload?sourceType=AML_CTR&outId=${baseInfo.cddFormId}`,
            headers: {
                authorization: 'authorization-text',
            },
            beforeUpload: async (file) => {
                console.log(file)
                let type = ['image/png', 'image/jpeg']
                if (type.includes(file.type)) {
                    /**
                     * 针对图片进行压缩,如果图片大小超过压缩阈值,则执行压缩,否则不压缩
                     */
                    let fileSize = file.size / 1024 / 1024;
                    if ((fileSize >= 1.5)) {
                        // 判断文件是不是jpeg 不是jpeg的都转成jpeg 
                        if (!['jpeg', 'jpg'].includes(file.type)) {
                            file = await ConvertImage(file);  //转陈jpeg格式的file
                        }
                        let newFile = await ImageCompressor(file, 'file', 0.6); //图片压缩
                        return newFile;
                    } else {
                        return file;
                    }
                } else {
                    return file
                }
            },
            onChange (info) {
                if (info.file.status !== 'uploading') {
                    // console.log(info.file, info.fileList);
                }
                if (info.file.status === 'done') {
                    console.log(info.file, info.fileList);
                    if (info.file.response && info.file.response.status === 200) {
                        _this.setState({
                            CTRdata: [..._this.state.CTRdata, info.file.response.data[0]],
                        });
                        Notification(`${info.file.name} file uploaded successfully`, 'success');
                    } else {
                        Notification(`${info.file.name}: ${info.file.response.message}`, 'error');
                    }
                } else if (info.file.status === 'error') {
                    Notification(`${info.file.name} file upload failed.`, 'error');
                }
            },
        };
        return (
            <div className="amlDetail">
                <Spin spinning={loading}>
                    <Form
                        {...layout}
                        name="record"
                        initialValues={{
                            outletCode: baseInfo.outletCode,
                            docNo: baseInfo.orderCode,
                            orderTime: moment(baseInfo.orderTime).format(format2),
                            employeeName: baseInfo.employeeName,
                            orderValue: baseInfo.orderValue
                                ? Number(baseInfo.orderValue).toFixed(2)
                                : 0,
                            orderDesc: baseInfo.orderDesc,
                            memberName: baseInfo.memberName,
                            // memberIdNumber: baseInfo.memberIdNumber,
                            memberNationalityName: baseInfo.memberNationalityName,
                        }}
                        ref={this.formRef}
                        colon={false}
                        requiredMark={false}
                    // onFinish={onFinish}
                    // onFinishFailed={onFinishFailed}
                    >
                        <Row>
                            <Col span={24}>
                                <div className="title">AML Record</div>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Holder" name="outletCode" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="DocNo." name="docNo" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="DocDate" name="orderTime" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="SalesPerson" name="employeeName" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="TotalAmount" name="orderValue" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="Description" name="orderDesc" {...layout}>
                                    <TextArea disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="FullName" name="memberName" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            {/* <Col span={8}>
                                <Form.Item label="IDNumber" name="memberIdNumber" {...layout}>
                                    <Input disabled />
                                </Form.Item>
                            </Col> */}
                            <Col span={8}>
                                <Form.Item
                                    label="Nationlity"
                                    name="memberNationalityName"
                                    {...layout}
                                >
                                    <Input disabled />
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item label="AML" name="otp" {...layout}>
                                    {/* <Input disabled /> */}
                                    <span
                                        className="checkSty"
                                        onClick={() => this.openModal(baseInfo.cddFormId)}
                                    >
                                        Check
                                    </span>
                                </Form.Item>
                            </Col>
                            {/* <Col span={8}>
                <Form.Item label="Signature" name="otp" {...layout}>
                  <span className="checkSty" onClick={() => this.openSign()}>
                    Check
                  </span>
                </Form.Item>
              </Col> */}
                        </Row>
                    </Form>
                    <Divider />
                    <div className="uptable">
                        <div className="title">AML Profiles</div>
                        <Table
                            columns={this.getcolumns(1)}
                            dataSource={PDFdata}
                            showSizeChanger={false}
                            size="small"
                            pagination={false}
                        />
                        <div className="uploadtitle">
                            <div className="title">CTR</div>
                            <Upload {...props} showUploadList={false} multiple={true}>
                                <Button icon={<UploadOutlined />} onClick={this.tosearch}>
                                    Upload Attachments
                                </Button>
                            </Upload>
                        </div>
                        <Table
                            columns={this.getcolumns(2)}
                            dataSource={CTRdata}
                            showSizeChanger={false}
                            size="small"
                            pagination={false}
                        />
                    </div>
                    <div className="footerBtn">
                        <Space>
                            <Button onClick={this.toback}>Cancel</Button>
                            <Button type="primary" onClick={this.tosaveAtta}>
                                Save
                            </Button>
                        </Space>
                    </div>
                    <Modal
                        title="Details"
                        visible={isModalVisible}
                        onCancel={this.handleCancel}
                        footer={null}
                        width={1000}
                        destroyOnClose
                    >
                        <AntiMoney
                            key={JSON.stringify(postMessagedata)}
                            disabled={
                                postMessagedata?.cddForm?.finishFill === false ? false : true
                            }
                            postMessagedata={postMessagedata}
                            closeModal={this.toClose}
                        />
                    </Modal>
                    <Modal
                        visible={previewVisible}
                        footer={null}
                        onCancel={this.handleCancel}
                        width={700}
                        destroyOnClose
                    >
                        <br />
                        <img alt="example" style={{ width: '100%' }} src={previewImage} />
                    </Modal>
                    {
                        <Image
                            width={200}
                            style={{
                                display: 'none',
                            }}
                            preview={{
                                visible: visibleImg,
                                src: previewImg,
                                onVisibleChange: () => {
                                    this.setState({ visibleImg: false, previewImg: '' });
                                },
                            }}
                        />
                    }
                </Spin>
            </div>
        );
    }
}

export default AmlDetail;
