/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react'
import { Switch, Route } from 'react-router-dom'

import allRouters from './config/route'

const App = () => {

    return (
        <Switch>
            {
                allRouters.map((obj, index) => (
                    <Route
                        exact={obj.exact}
                        key={index}
                        path={obj.path}
                        component={obj.component}
                    />
                ))
            }
        </Switch>
    )
}

export default App
