import React from 'react';
import {
    DatePicker,
    Form,
    Input,
    Button,
    Row,
    Col,
    Divider,
    Radio,
    Space,
} from 'antd';
import './index.less';
import SelectCommon from '@/components/selectCommon/index';
import moment from 'moment';
import { formatDate, formatDate_hh_mm_ss } from '@/utils/formatDate';

const { TextArea } = Input;


class AntiMoneyFormA extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      postMessagedata: this.props.postMessagedata,
      formRef: '',
      refArr: this.props.refArr,
      formdata: this.props.formdata,
      checkref: this.props.checkref,
      mIdType: true,
      disShowBoName:false
    };
  }

  formRef = React.createRef();

  componentDidMount() {
    console.log(this.props);
    console.log(this.formRef);
    this.setState({ formRef: this.formRef });
    this.formRef.current.resetFields();
    if (
      this.props.formdata.formA &&
      this.props.formdata.formA.mIdType === 'NRIC'
    ) {
      this.setState({ mIdType: false });
    }
    if(this.props.formdata?.formA?.boCaa){
      this.setState({disShowBoName:this.props.formdata?.formA?.boCaa})
    }
    // this.props.formA(this);
  }

    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        if (
            JSON.stringify(nextProps.postMessagedata) !== this.props.postMessagedata
        ) {
            this.setState({ postMessagedata: nextProps.postMessagedata }, () => {
                this.formRef.current.resetFields();
            });
        }
        if (JSON.stringify(nextProps.refArr) !== this.props.refArr) {
            this.setState({ refArr: nextProps.refArr }, () => { });
        }
        if (JSON.stringify(nextProps.checkref) !== this.props.checkref) {
            this.setState({ checkref: nextProps.checkref }, () => { });
        }
        if (JSON.stringify(nextProps.formdata) !== this.props.formdata) {
            this.setState({ formdata: nextProps.formdata }, () => {
                if (
                    nextProps.formdata.formA &&
                    nextProps.formdata.formA.mIdType === 'NRIC'
                ) {
                    this.setState({ mIdType: false });
                }
                this.formRef.current.resetFields();
            });
        }
      }
  jumpNext = () => {
    // let postMessageData = {
    //   key: 'finish',
    // };
    // window.parent.postMessage(JSON.stringify(postMessageData), '*');
    this.formRef.current
      .validateFields()
      .then((values) => {
        console.log(values);
        this.props.saveFormData(values, 'formA');
        // if (values.mName !== values.boName && values.boCaa) {
        if (!values.boCaa) {
          this.props.changeShowtype('formBod');
        } else {
          this.props.saveFormData(false, 'formBod');
          this.props.changeShowtype('formB');
        }
      })
      .catch((errorInfo) => {
        console.log(errorInfo);
      });
    // this.props.changeShowtype('formB');
    // this.props.changeShowtype('formB');
  };

  toGetname = (e) => {
    console.log(e.target.value);
    if (e.target.value) {
      let mName = this.formRef.current.getFieldValue('mName');
      this.formRef.current.setFieldsValue({
        boName: mName,
      });
      this.setState({disShowBoName:true})
    }else{
      this.setState({disShowBoName:false})
      this.formRef.current.setFieldsValue({
        boName: '',
      });
    }
  }

    getDesc = (data) => {
        let resule = [];
        data.retailVouchDetails.map((ele, index) => {
            resule.push(
                index + 1 + '：' + ele.productsku_cCode + ' ' + ele.product_cName
            );
            return false
        });
        return resule.join('\n');
    };

    changemIdType = (e) => {
        console.log(e);
        this.setState({ mIdType: e.target.value === 'NRIC' ? false : true });
    };

    toClose = () => {
        let postMessageData = {
            key: 'close',
        };
        window.parent.postMessage(JSON.stringify(postMessageData), '*');
    };

    render() {
        let { disShowBoName,postMessagedata, formRef, refArr, formdata, checkref, mIdType } =
            this.state;
        const layout = {
            labelCol: {
                span: 10,
            },
            wrapperCol: {
                span: 14,
            },
        };
        const layout2 = {
            labelCol: {
                span: 5,
            },
            wrapperCol: {
                span: 19,
            },
        };
        const ruleConfig = {
            rules: [{ required: true, message: 'Please enter required fields' }],
        };
        const ruleConfigselect = {
            rules: [{ required: true, message: 'Please select required fields' }],
        };
        const disabledConfig = {
            disabled: formdata.disabled ? formdata.disabled : false,
        };
        let initialValuesConfig =
            formdata && formdata.formA
                ? {
                    ...formdata.formA,
                    txnValue: formdata
                        ? formdata.formA.txnValue
                            ? Number(formdata.formA.txnValue).toFixed(2)
                            : ''
                        : '',
                    newmBirth: formdata
                        ? formdata.formA.mBirth
                            ? moment(formdata.formA.mBirth).format(formatDate)
                            : ''
                        : '',
                    newmIdExpiryDate: formdata
                        ? formdata.formA.mIdExpiryDate
                            ? moment(formdata.formA.mIdExpiryDate).format(formatDate)
                            : ''
                        : '',
                    mNationalityId:
                        formdata && formdata.formA && formdata.formA.mNationalityId
                            ? formdata.formA.mNationalityId.toString()
                            : '',
                    mOccupationId:
                        formdata && formdata.formA && formdata.formA.mOccupationId
                            ? formdata.formA.mOccupationId.toString()
                            : '',
                }
                : {
                    businesstypeId: postMessagedata.formData
                        ? postMessagedata.formData.iBusinesstypeid
                        : '',
                    businesstypeName: postMessagedata.formData
                        ? postMessagedata.formData.iBusinesstypeid_name
                        : '',
                    outletCode: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch.cStoreCode
                        : '',
                    outletId: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch.store
                        : '',
                    cddDate: postMessagedata.data
                        ? moment(postMessagedata.data.rm_retailvouch.createTime).format(formatDate_hh_mm_ss)
                        : '',
                    employeeName: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch
                            .retailVouchHeaderExtend_Employeename
                        : '',
                    employeeId: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch
                            .retailVouchHeaderExtend_Employeeid
                        : '',
                    txnTime: postMessagedata.data
                        ? moment(postMessagedata.data.rm_retailvouch.createTime).format(formatDate_hh_mm_ss)
                        : '',
                    txnValue: postMessagedata.data
                        ? postMessagedata.data.rm_retailvouch.fGatheringMoney
                            ? Number(
                                postMessagedata.data.rm_retailvouch.fGatheringMoney
                            ).toFixed(2)
                            : 0
                        : '',
                    txnDesc: postMessagedata.data
                        ? this.getDesc(postMessagedata.data.rm_retailvouch)
                        : '',
                    mName: postMessagedata.memberInfo
                        ? postMessagedata.memberInfo.data.realname
                        : '',
                    mBirth: postMessagedata.memberInfo
                        ? moment(postMessagedata.memberInfo.data.birthday)
                        : '',
                    mPhone: postMessagedata.memberInfo
                        ? postMessagedata.memberInfo.data.phone
                        : '',
                    mNationalityId: postMessagedata.memberInfo
                        ? postMessagedata.memberInfo.data.define1
                        : '',
              txnNum: postMessagedata?.formData?.code || '',
              orderId: postMessagedata?.formData?.id || '',
                };
        return (
          <div className="antiMoneyFormA">
            <Form
              {...layout}
              name="formA"
              initialValues={{
                ...initialValuesConfig,
              }}
              size='small'
              ref={this.formRef}
              colon={false}
              requiredMark={false}
              // onFinish={onFinish}
              // onFinishFailed={onFinishFailed}
            >
              <Row>
                <Col span={24}>
                  <div className="title">
                    Customer Due Diligence(CDD)-FORM A
                  </div>
                </Col>
                <Col span={12}>
                  {/* 交易类型 龟龟，这些字段都是些啥啊*/}
                  <Form.Item
                    {...ruleConfig}
                    label="Transaction Type"
                    name="businesstypeName"
                  >
                    <Input disabled />
                  </Form.Item>
                  <Form.Item
                    {...ruleConfig}
                    label="Transaction Type"
                    name="businesstypeId"
                    hidden
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    //  {...ruleConfig}
                    label="Record No."
                    name="orderId"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Outlet" name="outletCode">
                    <Input disabled />
                  </Form.Item>
                  <Form.Item
                    {...ruleConfig}
                    label="Outlet"
                    name="outletId"
                    hidden
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="Date" name="cddDate">
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Name of Employee"
                    name="employeeName"
                    {...layout2}
                  >
                    <Input disabled />
                  </Form.Item>
                  <Form.Item
                    {...ruleConfig}
                    label="Name of Employee"
                    name="employeeId"
                    {...layout2}
                    hidden
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Divider />
                <div className="title">
                  Al. Customers Particulars(For Individuals)
                </div>
                <Col span={24}>
                  {/* 交易编号 */}
                  <Form.Item
                    // {...ruleConfig}
                    label="Transaction Number"
                    name="txnNum"
                    {...layout2}
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...ruleConfig}
                    label="Date/Time of Transaction"
                    name="txnTime"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...ruleConfig}
                    label="Transaction value"
                    name="txnValue"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Description of PSPM"
                    name="txnDesc"
                    {...layout2}
                  >
                    {this.props.printnow ? (
                      <div className="textareareplace">
                        {initialValuesConfig?.txnDesc || ''}
                      </div>
                    ) : (
                      <TextArea autoSize disabled />
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...ruleConfig}
                    label="Full Name(including any alias)"
                    name="mName"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  {this.props.printnow ? (
                    <Form.Item
                      {...ruleConfig}
                      label="Date Of Birth"
                      name="newmBirth"
                    >
                      <Input disabled />
                    </Form.Item>
                  ) : (
                    <Form.Item
                      {...ruleConfig}
                      label="Date Of Birth"
                      name="mBirth"
                    >
                      <DatePicker format={formatDate} disabled />
                    </Form.Item>
                  )}
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Residential Address"
                    name="mAddress"
                    {...layout2}
                  >
                    <TextArea {...disabledConfig} />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...ruleConfig}
                    label="Contact Number"
                    name="mPhone"
                  >
                    <Input disabled />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...ruleConfigselect}
                    label="Nationality"
                    name={
                      this.props.printnow
                        ? 'mNationalityName'
                        : 'mNationalityId'
                    }
                  >
                    {this.props.printnow ? (
                      <Input disabled />
                    ) : (
                      <SelectCommon
                        type="mNationalityId"
                        refArr={refArr.Nationality ? refArr.Nationality : []}
                        formRef={formRef.current}
                        disabled
                        // value = {postMessagedata.memberInfo?postMessagedata.memberInfo.data.define1:''}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  {/* 证件类型 */}
                  <Form.Item
                    {...ruleConfigselect}
                    name="mIdType"
                    label="Type of ID Document"
                    {...layout2}
                  >
                    <Radio.Group
                      {...disabledConfig}
                      onChange={this.changemIdType}
                    >
                      {checkref.id_type
                        ? checkref.id_type.map((ele) => {
                            return <Radio key={ele.code} value={ele.code}>{ele.name}</Radio>;
                          })
                        : ''}
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item {...ruleConfig} label="ID Number" name="mIdNumber">
                    <Input {...disabledConfig} />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  {/* {JSON.stringify(mIdType)}
                    {JSON.stringify(!initialValuesConfig?.mIdType)}
                    {JSON.stringify(initialValuesConfig?.mIdType)} */}
                  {(mIdType && !this.props.printnow) ||
                  (formdata.disabled &&
                    initialValuesConfig?.mIdType !== 'NRIC') ? (
                    <Form.Item
                      rules={[
                        {
                          validator: (_, value) => {
                            console.log(value);
                            if (!value) {
                              return Promise.reject(
                                new Error('Please enter required fields')
                              );
                            } else {
                              // if (moment(value).diff(moment(), 'month') < 6) {
                              //   return Promise.reject(
                              //     new Error(
                              //       'The date of ID expiry cannot be less than 6 months from the current date'
                              //     )
                              //   );
                              // }
                              return Promise.resolve();
                            }
                          },
                        },
                      ]}
                      label="Date Of ID Expiry"
                      name={
                        this.props.printnow
                          ? 'newmIdExpiryDate'
                          : 'mIdExpiryDate'
                      }
                    >
                      {this.props.printnow ? (
                        <Input disabled />
                      ) : (
                        <DatePicker format={formatDate} {...disabledConfig} />
                      )}
                    </Form.Item>
                  ) : (
                    ''
                  )}
                </Col>
                <Col span={24}>
                  {/* 等接口 */}
                  <Form.Item
                    {...ruleConfigselect}
                    label="Occupation"
                    name={
                      this.props.printnow ? 'mOccupationName' : 'mOccupationId'
                    }
                    {...layout2}
                  >
                    {this.props.printnow ? (
                      <Input disabled />
                    ) : (
                      <SelectCommon
                        type="mOccupationId"
                        refArr={refArr.Occupation ? refArr.Occupation : []}
                        formRef={formRef.current}
                        {...disabledConfig}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    name="boCaa"
                    label="Customer Buy For Ownself?"
                    {...layout2}
                  >
                    <Radio.Group onChange={this.toGetname} {...disabledConfig}>
                      {/* 等接口 */}
                      <Radio value={true}>Yes</Radio>
                      <Radio value={false}>No</Radio>
                    </Radio.Group>
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...ruleConfig}
                    label="Name of Beneficial Owner"
                    name="boName"
                    {...layout2}
                  >
                    <Input
                      disabled={
                        formdata.disabled || disShowBoName ? true : false
                      }
                    />
                  </Form.Item>
                </Col>
              </Row>

              <Col span={24}>
                <Form.Item
                  // {...ruleConfig}
                  label="Other Information"
                  name="otherInfo"
                  {...layout2}
                >
                  <Input {...disabledConfig} />
                </Form.Item>
              </Col>
              <Divider />
            </Form>
            <div className="btn">
              {this.props.printnow ? (
                ''
              ) : (
                <Space>
                  {formdata.disabled === undefined && (
                    <Button size='small' onClick={this.toClose}>Close</Button>
                  )}
                  <Button size='small' onClick={this.jumpNext}>Next</Button>
                </Space>
              )}
            </div>
          </div>
        );
    }
}

export default AntiMoneyFormA;
