import React from 'react';
import ReactDOM from 'react-dom'
import { Spin } from "antd";
import axios from "axios";
import Notification from '@/components/Notification/index'
// import * as models from '../models/index'
// import { init } from '@rematch/core'
import storageUtils from "@/utils/storageUtils";

// const store = init({
//     models
// })


//第一步，创建实例
const Axios = axios.create({
    baseURL: process.env.REACT_APP_API,
    timeout: 600000,
    headers: {
        'Content-Type': 'application/json; charset=utf-8'
    }
});
/* 
 * removeAllPendingRequestsRecord 取消请求操作
 * func 取消请求操作
 * func 移除所有记录
*/
const allPendingRequestsRecord = [];
const removeAllPendingRequestsRecord = () => {
    allPendingRequestsRecord && allPendingRequestsRecord.forEach((func) => {
        func('路由跳转了取消所有请求');
    });
    allPendingRequestsRecord.splice(0);
};

// 取消所有请求的函数
export const getConfirmation = (mes = '', callback = () => { }) => {
    removeAllPendingRequestsRecord();
    callback();
};

/* 
 * removeRepeatUrl 取消同一个重复的ajax请求
 * pending 声明一个数组用于存储每个ajax请求的取消函数和ajax标识
 * func 移除所有记录
*/
let pending = [];
let removeRepeatUrl = ({ url, method }) => {
    for (let p in pending) {
        if (pending[p].u === url + '&' + method) {
            pending[p].f();
            pending.splice(p, 1);
        }
    }
}

/* 
 * 第二步，请求拦截（请求头）
 * requestCount 当前正在请求的数量
 * removeRepeatUrl 在一个ajax发送前执行一下取消操作
*/
let requestCount = 0
let jo_token = ''

Axios.interceptors.request.use(config => {
    if (typeof window !== "undefined") {
        jo_token = storageUtils.getToken() && storageUtils.getToken()?.token
    }

    config.headers.yht_access_token = config.headers_anti ? config.headers_anti.yht_access_token : jo_token
    config.headers.tenantId = config.headers_anti ? config.headers_anti.tenantId : config.headers.tenantId

    if (config.headers.isLoading === true) {
        showLoading()
    }
    removeRepeatUrl(config);

    // 取消请求
    config.cancelToken = new axios.CancelToken((c) => {
        // 自定义唯一标识
        setTimeout(() => {
            pending.push({ u: config.url + '&' + config.method + config.data, f: c });
            allPendingRequestsRecord.push(c);
        })
    });

    return config
}, err => {
    if (err.config.headers.isLoading === true) {
        hideLoading()
    }
    return Promise.reject(err)
});

// 第三步，响应拦截（响应头）
Axios.interceptors.response.use(res => {
    // 在一个ajax响应后再执行一下取消操作，把已经完成的请求从pending中移除
    removeRepeatUrl(res.config);
    if (res.config.headers.isLoading === true) {
        hideLoading()
    }
    if (res.status === 200) {
        if (+res.data.status === 200) {
            return res.data
        } else {
            // 判断token是否过期
            // if (res.data.message.indexOf('用户未登录')) {
            //     store.dispatch.auth.setIsUser({ logged: false })
            //     removeLocal('JO_')
            // }
            return Notification(res.data.message, 'error')
        }
    } else {
        return Notification(res.error, 'error')
    }
}, error => {
    // 终结由于取消重复请求而引发的报错提示
    if (axios.isCancel(error)) {
        return new Promise(() => { });
    }
    hideLoading()

    if (error.response) {
        return Notification('Gateway Timeout!', 'error');
    }
    if (error.code === "ECONNABORTED") {
        return Notification('Network Timeout!', 'error');
    }
    return Promise.reject(error)
});

// 显示loading
function showLoading () {
    if (requestCount === 0) {
        var dom = document.createElement('div')
        dom.setAttribute('id', 'glob-loading')
        dom.setAttribute('style', 'position: fixed;width: 100%;height: 100%; top:0;background-color:rgba(255,255,255,0.7);');
        document.body.appendChild(dom)
        document.body.style.overflow = 'hidden'
        document.body.style.height = '100%'
        ReactDOM.render(<Spin size="large" delay={500} />, dom)
    }
    requestCount++
}

// 隐藏loading
function hideLoading () {
    requestCount--
    if (requestCount === 0) {
        document.body.style.overflow = 'auto'
        document.body.removeChild(document.getElementById('glob-loading'))
    }
}

export default Axios